<template>
  <div>
    <header class="clear"> 余额 {{residueMoney}} <el-button type="primary" size="small" @click="outMoney" class="right"> 提现 </el-button> </header>
    <section>
      <el-table
        ref="multipleTable"
        :data="tableData"
        style="width: 100%"
      >
        <el-table-column
          v-for="(item,key) in columns"
          :key="key"
          :prop="item.prop"
          :label="item.label"
        >
        </el-table-column>
      </el-table>
    </section>

    <footer>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </footer>

    <el-dialog
      :visible="visible"
      :before-close="handleClose"
    >
      <el-form ref="form"
               :model="ruleForm"
               :rules="rules"
               class="add-actively-form"
               label-width="80px"
      >
        <el-form-item
          label="提现金额"
          prop="withdrawMoney"
        >
          <el-input-number :max="99999999" v-model="ruleForm.withdrawMoney" size="small" placeholder="最多8位数">

          </el-input-number>
        </el-form-item>

        <el-form-item
          label="户主"
          prop="accountUser"
        >
          <el-input size="small" v-model="ruleForm.accountUser" placeholder="1-10字">

          </el-input>
        </el-form-item>
        <el-form-item
          label="开户银行"
          prop="bank"
        >
          <el-input v-model="ruleForm.bank" size="small" placeholder="50字">
          </el-input>
        </el-form-item>

        <el-form-item
          label="银行账号"
          prop="bankAccount"
        >
          <el-input size="small" v-model="ruleForm.bankAccount" placeholder="最多20位" >
          </el-input>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="houseSubmit" :disabled="submiting" :loading="submiting">提现</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

  import util from '@/utils/util'
  import moment from 'moment';

  export default{

    creted(){

    },

    mounted(){
      this.reqList();
    },

    data(){

      var validatorNumber = (rule, value, callback) => {
        const r1 = /^\d*[^.]$/;
        const r2 = /^\d*\.?\d{1,2}$/;

        if (r1.test(value) || r2.test(value)) {
          callback();
        } else {
          callback(new Error('请保留两位小数'));
        }
      };


      return {
        submiting:false, /* 提交中 */
        rules:{
          withdrawMoney: [
            { validator: validatorNumber, trigger: 'blur' },
          ],
          bank: [
            { required: true, message:'请填写', trigger: 'blur' },
            { type:'string', message:'1-10个字', max:10, trigger: 'blur' },
          ],
          bankAccount: [
            { required: true, message:'请填写', trigger: 'blur' },
            { type:'string', message:'1-50个字', max:50, trigger: 'blur' },
          ],
          accountUser: [
            { required: true, message:'请填写', trigger: 'blur' },
            { type:'string', message:'1-20个字', max:20, trigger: 'blur' },
          ]
        },
        ruleForm:{
          withdrawMoney:'',
          bank:'',
          bankAccount:'',
          accountUser:'',
        },
        visible:false,
        tableData:[],
        total:0,
        currentPage:1,
        residueMoney:null, /* 余额 */

        columns:[
          {label:"活动ID",prop:"activityId"},
          {label:"活动名称",prop:"activityName"},
          {label:"投放小区",prop:"cName"},
          {label:"投放时间",prop:"startDate"},
          {label:"领取人数",prop:"count"},
          {label:"佣金分红（元）",prop:"propertyMoney"},
        ],
      }
    },

    methods:{
      outMoney(){
        this.visible = true;
      },
      handleClose(){
        this.visible = false;
        this.$refs.form.resetFields('ruleForm');
      },
      houseSubmit() {
        /* 提现 */
        if (Number(this.ruleForm.withdrawMoney) === 0) {
          this.$message('请输入有效提现金额!');
            return;
        }
        if ( this.residueMoney < this.ruleForm.withdrawMoney ) {
            this.$message('可提现金额不能大于余额!');
            return;
        }
        this.$nextTick(() => {
          this.$refs.form.validate((valid)=>{
            if(valid){
              this.submiting = true;
              this.$http.post('/finance/withdraw/property/apply',{
                propertyId:util.getSession('enId'),
                ...this.ruleForm
              }).then(res => {
                if (res.errorCode === '0') {
                  this.$message('提现成功');
                  setTimeout(() => {
                    this.submiting = false;
                    this.handleClose();
                    this.reqList();
                  },500)
                }else {
                  this.submiting = false;
                }
              }).catch((err) => {
                setTimeout(() => {
                  this.submiting = false;
                },500)
              })
            }
          })
        })

      },
      handleCurrentChange(page){
        this.reqList(page);
      },
      reqList(pageNum=1,pageSize=10) {
        this.$http.get("/activitystatistics/property",{
          params:{
            pageNum,
            pageSize,
            enCode:util.getSession("enCode"),
          }
        }).then((res)=>{

          res = res.resultData;

          const data = res.padms;

          data.forEach((i) => {
              i.startDate = moment(i.startDate).format('YYYY-MM-DD');
          })

          this.residueMoney = res.residueMoney;

          this.total = res.total;

          this.tableData = res.padms;

        })
      }
    },
  }

</script>

<style scoped>
  section{
    margin:20px 0;
  }
  footer{
    text-align: right;
  }
</style>



