<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  import TopBar from '../../components/topBar.vue'

  export default {
    name: 'app',
    components: {
      TopBar
    },
    data () {
      return {
        login: true,
        type: 'advertiser'
      }
    }
  }
</script>

<style>
  .el-main {
    min-height: 84vh;
  }
  .advertiser-section-list{
    text-align:left;
    margin:15px 0;
    font-size:14px;
  >li{
     display: inline-block;
     margin-right:20px;
   }
  }
</style>



