<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label"
      >
      </el-table-column>

      <el-table-column
        prop="status"
        label="状态"
      >
        <template slot-scope="scope">
          <span> {{ scope.row.status === 0 ? '待处理' : '提现成功'}} </span>
        </template>
      </el-table-column>
    </el-table>

    <footer>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </footer>
  </div>
</template>

<script>

  import util from '@/utils/util';

  export default{
    data(){
      return {
        tableData:[],
        currentPage:1,
        total:0,
        columns:[
          {label:"流水号",prop:"serialNumber"},
          {label:"提现金额（元）",prop:"withdrawMoney"},
          {label:"开户银行",prop:"bank"},
          {label:"银行卡号",prop:"bankAccount"},
          {label:"户名",prop:"accountUser"},
        ],
      }
    },

    mounted(){
      this.reqList();
    },

    methods:{
      reqList(pageNum=1,pageSize=10) {
          this.$http.get('/property/withdraw/list',{
              params:{
                pageNum,
                pageSize,
                propertyId:util.getSession('enId')
              }
          }).then(res => {

            res = res.resultData;

            this.total = res.total;

            this.tableData = res.propertyWithdraws || [];

          })
      },
      handleCurrentChange(page){
        this.reqList(page);
      },
    }
  }

</script>

<style scoped>
  footer{
    text-align: right;
    margin-top: 20px;
  }
</style>



