import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'

// 小区管理
import DistrictManagement from '../views/districtManagement/index'
import AddDistrict from '../views/districtManagement/addDistrict'
import DistrictDetail from '../views/districtManagement/districtDetail'

// 物业管理
import PropertyManagement from '../views/propertyManagement'
import IncomeDetails from '../views/propertyManagement/incomeDetails'

// 广告主管理
import AdvertiserManagement from '../views/advertiserManagement'
import AddAdvertiser from '../views/advertiserManagement/addAdvertiser.vue'

// 用户管理
import UserManagement from '../views/userManagement.vue'

// 权限管理
import PermissionManagement from '../views/permissionManagement/index'
import AddPersonnel from '../views/permissionManagement/addPersonnel.vue'

// 发布活动
import MadeActively from '../views/madeActivity/index.vue'
import AddMadeActivity from '../views/madeActivity/addActivity.vue'
import EditMadeActivity from  '../views/madeActivity/editActivity'
import DetailMadeActivity from '../views/madeActivity/detail.vue'

// 财务管理
import PropertyPresentAudit from '../views/financialManagement/propertyPresentAudit'
import UserPresentAudit from '../views/financialManagement/userPresentAudit'
import PlatformPumping from '../views/financialManagement/platformPumping'

// 企业账号
import EnterpriseAccount from '../views/enterpriseAccount'
import AddEnterpriseAccount from '../views/enterpriseAccount/addEnterpriseAccount.vue'

// 意见反馈
import OpinionFeedback from '../views/opinionFeedback.vue'

// 充值记录
import RechargeRecord from '../views/rechargeRecord.vue'

// 话费设置
import CallSetting from '../views/callSetting.vue'

// 流量设置
import TrafficSetting from '../views/trafficSetting.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/districtManagement',
      name: '小区管理',
      component: DistrictManagement,
      meta: { requiresAuth: true, pid: '1' }
    },
    {
      path: '/propertyManagement',
      name: '物业管理',
      component: PropertyManagement,
      meta: { requiresAuth: true, pid: '10' }
    },
    {
      path: '/userManagement',
      name: '用户管理',
      component: UserManagement,
      meta: { requiresAuth: true, pid: '4' }
    },
    {
      path: '/advertiserManagement',
      name: '广告主管理',
      component: AdvertiserManagement,
      meta: { requiresAuth: true, pid: '2' }
    },
    {
      path: '/addAdvertiser',
      name: '新增广告主',
      component: AddAdvertiser,
      meta: { requiresAuth: true, pid: '2' }
    },
    {
      path: '/addDistrict',
      name: '添加小区',
      component: AddDistrict,
      meta: { requiresAuth: true, pid: '1' }
    },
    {
      path: '/districtDetail',
      name: '小区详情',
      component: DistrictDetail,
      meta: { requiresAuth: true, pid: '1' }
    },
    {
      path: '/incomeDetails',
      name: '收入明细',
      component: IncomeDetails,
      meta: { requiresAuth: true, pid: '10' }
    },
    {
      path: '/login',
      name: '登录',
      alias:"/",
      component: Login,
      meta: { loginType: 0 }
    },
    {
      path: '/permissionManagement',
      name: '权限管理',
      component: PermissionManagement,
      meta: { requiresAuth: true, pid: '9' }
    },
    {
      path: '/addpersonnel',
      name: '权限管理-添加人员',
      component: AddPersonnel,
      meta: { requiresAuth: true, pid: '9' }
    },
    {
      path: '/made_actively',
      name: '发布活动-主页',
      component: MadeActively,
      meta: { requiresAuth: true, pid: '3' }
    },
    {
      path: '/made_actively_add',
      name: '发布活动-添加',
      component: AddMadeActivity,
      meta: { requiresAuth: true, pid: '3' }
    },
    {
      path: '/made_actively_edit/:id',
      name: '发布活动-编辑',
      component:EditMadeActivity,
      meta: { requiresAuth: true, pid: '3' }
    },
    {
      path: '/made_actively_detail/:id',
      name: '发布活动-详情',
      component:DetailMadeActivity,
      meta: { requiresAuth: true, pid: '3' }
    },
    {
      path: '/propertyPresentAudit',
      name: '财务管理-财务提现审核',
      component: PropertyPresentAudit,
      meta: { requiresAuth: true, pid: '7' }
    },
    {
      path: '/userPresentAudit',
      name: '财务管理-用户体现审核',
      component: UserPresentAudit,
      meta: { requiresAuth: true, pid: '7' }
    },
    {
      path: '/platformPumping',
      name: '财务管理',
      component: PlatformPumping,
      meta: { requiresAuth: true, pid: '7' }
    },
    {
      path: '/enterpriseAccount',
      name: '企业账号',
      component: EnterpriseAccount,
      meta: { requiresAuth: true, pid: '8' }
    },
    {
      path: '/addEnterpriseAccount',
      name: '企业账号-添加',
      component: AddEnterpriseAccount,
      meta: { requiresAuth: true, pid: '8' }
    },
    {
      path: '/opinionFeedback',
      name: '意见反馈',
      component: OpinionFeedback,
      meta: { requiresAuth: true, pid: '6' }
    },
    {
      path: '/rechargeRecord',
      name: '充值记录',
      component: RechargeRecord,
      meta: { requiresAuth: true, pid: '5' }
    },
    {
      path: '/callSetting',
      name: '话费设置',
      component: CallSetting,
      meta: { requiresAuth: true, pid: '11' }
    },
    {
      path: '/trafficSetting',
      name: '流量设置',
      component: TrafficSetting,
      meta: { requiresAuth: true, pid: '12'}
    }
  ]
})

router.beforeEach((to, from, next) => {

  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!sessionStorage.getItem('userInfo')) {
      next({
        path: '/login'
      })
    } else {
      let pidsArr = JSON.parse(sessionStorage.getItem('userInfo')).pids.split(',')

      if ((from.path === '/login' || from.path === '/') && sessionStorage.getItem('userInfo')) {
        next(false)
      }

      if (from.path !== '/login' && pidsArr.indexOf(to.meta.pid) === -1) {
        next(false)
      } else {
        next()
      }
    }
  } else {
    next()
  }
})

export default router



