<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <!--<Header></Header>-->
    <!--<SideBar></SideBar>-->
    <Login v-if="login"></Login>
    <el-container v-else>
      <el-header>
        <TopBar @updateLoginStatus="updateLoginStatus"></TopBar>
      </el-header>
      <el-container>
        <el-aside>
          <SideBar></SideBar>
        </el-aside>
        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
    <!---->
  </div>
</template>

<script>
  import Login from '../../components/login.vue'
  import SideBar from '../../components/sideBar.vue'
  import TopBar from '../../components/topBar.vue'

  export default {
    name: 'app',
    components: {
      SideBar, TopBar, Login
    },
    data () {
      return {
        login: true
      }
    },
    mounted: function () {

      if (!sessionStorage.getItem('userInfo')) {
        this.login = true
      } else {
        this.login = false
      }
    },
    methods: {
      updateLoginStatus (msg) {
        this.login = msg
      }
    }
  }
</script>



