<template>
  <div id="addPersonnel" class="addPersonnel">
    <h3 v-if="uid === 0">添加人员</h3>
    <h3 v-else>编辑人员</h3>
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      class="demo-ruleForm"
      label-position="center"
      size="small"
    >
      <el-form-item label="用户名" prop="userName">
        <el-input v-model="ruleForm2.userName" placeholder="1-10字"></el-input>
      </el-form-item>
      <el-form-item label="登录账号" prop="account">
        <el-input v-model.number="ruleForm2.account" placeholder="请填写手机号"></el-input>
      </el-form-item>
      <el-form-item label="登录密码" prop="password">
        <el-input v-bind:disabled="disabled" type="password" placeholder="6-20位数字或字母" v-model="ruleForm2.password" auto-complete="off"></el-input>
        <el-button size="small" v-if="disabled === true" @click="resetPass">重置密码</el-button>
        <span style="color: red">{{ resetHint }}</span>
      </el-form-item>
      <br>
      <h3>分配权限</h3>
      <el-form-item prop="defaultCheckList">
        <el-checkbox-group
          @change="checkboxChange"
          v-model="ruleForm2.defaultCheckList"
        >
          <el-checkbox v-for="(c,key) in checkList" :key="key" v-bind:label="c.pid.toString()">{{ c.name }}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <div class="operationBtn">
        <el-form-item>
          <el-button @click="back">返回</el-button>
          <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'addPersonnel',
    data () {
      var validateUserName = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入用户名'))
        } else if (value.length > 10) {
          callback(new Error('最多输入10字符'))
        } else {
          callback()
        }
      }
      var validateAccount = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入登录账号'))
        } else if (!/^1[3,4,5,6,7,8]\d{9}$/.test(value)) {
          callback(new Error('请输入正确手机号'))
        } else {
          callback()
        }
      }
      var validatePassword = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入登录密码'))
        } else if (value.length < 6 || value.length > 20) {
          callback(new Error('输入6-20登录密码'))
        } else {
          callback()
        }
      }
      var validateDefaultCheckList = (rule, value, callback) => {
        if (!value[0]) {
          callback(new Error('请选择权限'))
        } else {
          callback()
        }
      }
      return {
        checkList: [],
        uid: this.$route.query.uid || 0,
        disabled: false,
        resetHint: '',
        ruleForm2: {
          userName: '',
          account: '',
          password: '',
          defaultCheckList: []
        },
        rules2: {
          userName: [
            { required: true, validator: validateUserName, trigger: 'change' }
          ],
          account: [
            { required: true, validator: validateAccount, trigger: 'change' }
          ],
          password: [
            { required: true, validator: validatePassword, trigger: 'change' }
          ],
          defaultCheckList: [
            { required: true, validator: validateDefaultCheckList, trigger: 'change' }
          ]
        }
      }
    },
    created () {
      let _this = this
      this.$http('/backuser/getPids', {
        params: {}
      })
        .then(function (res) {
          if (res.errorCode === '0') {
            let pids = res.resultData ? res.resultData.pids : []
            _this.checkList = pids
            return _this.checkList
          }
        })
        .catch(function (err) {
          console.log(err)
        })
      if (this.uid !== 0) {
        this.get()
        this.disabled = true
        return this.disabled
      }
    },
    methods: {
      get () {
        let _this = this
        this.$http('/backuser/get', {
          params: {
            uid: this.uid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let backuser = res.resultData.backuser
              _this.ruleForm2.userName = backuser.userName
              _this.ruleForm2.account = backuser.account
              _this.ruleForm2.password = '1234567890'
              _this.ruleForm2.defaultCheckList = backuser.pids === '' ? [] : backuser.pids.split(',')
              return _this
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      checkboxChange (value) {
        this.defaultCheckList = value
        return this.defaultCheckList
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.saveHttp()
          } else {
            return false
          }
        })
      },
      back () {
        this.$router.push('/permissionManagement')
      },
      saveHttp () {
        let _this = this
        this.$http.get('/backuser/save', {
          params: {
            userName: this.ruleForm2.userName,
            account: this.ruleForm2.account,
            password: this.ruleForm2.password,
            type: 0,
            uid: this.uid === 0 ? '' : this.uid,
            pids: this.ruleForm2.defaultCheckList.join(',')
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '保存成功',
                type: 'success'
              })
              // 如果修改的是自己的权限，则修改本地存储的数据
              let userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
              if (userInfo.uid === _this.uid) {
                userInfo.pids = _this.ruleForm2.defaultCheckList.join(',')
                sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
              }
              _this.$router.push('permissionManagement')
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      resetPass () {
        let _this = this
        this.$http.get('/backuser/resetPassword', {
          params: {
            uid: this.uid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.resetHint = '密码已重置为123456'
              return _this.resetHint
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style>
  .addPersonnel ul {

  }
  .addPersonnel >div>.el-checkbox {
    margin-bottom: 10px;
  }
  .addPersonnel ul li {
    margin: 10px;
    line-height: 32px;
    clear: both;
  }
  .addPersonnel ul li > i {
    font-style: normal;
    color: red;
  }
  .addPersonnel ul li > span {
    float: left;
    width: 90px;
    text-align: right;
  }
  .addPersonnel ul li > span::before {
    content: '*';
    color: red;
  }
  .addPersonnel ul li > div {
    width: 200px;
  }
  .addPersonnel .operationBtn {
    text-align: center;
    margin-top: 60px;
  }
  .addPersonnel .operationBtn button {
    margin-right: 20px;
    padding: 10px 40px;
  }
  .addPersonnel .el-checkbox {
    margin-right: 30px;
  }
  .addPersonnel .el-checkbox+.el-checkbox {
    margin-left: 0;
  }
  .addPersonnel .el-input {
    width: 250px;
  }
</style>



