import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login'
import PartAdvertiser from '../pages/partAdvertiser.vue'
import OutMoneyHistory from '../pages/outMoneyHistory.vue'
import AdvertiserHarvest from '../pages/advertiserHarvest.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/login',
      name: '登录',
      component: Login,
      meta: { loginType: 2 }
    },
    {
      path: '/advertiserHarvest',
      alias:"/",
      name: '广告收益',
      component: AdvertiserHarvest,
      meta: { requiresAuth: true }
    },
    {
      path: '/outMoneyHistory',
      name: '提现记录',
      component: OutMoneyHistory,
      meta: { requiresAuth: true }
    },
    {
      path: '/partAdvertiser',
      name: '管辖小区',
      component: PartAdvertiser,
      meta: { requiresAuth: true }
    },
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!sessionStorage.getItem('userInfo')) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router



