<template>
  <div class="add-actively">

    <el-form ref="form"
             class="add-actively-form"
             :model="form"
             label-width="80px"
    >
      <el-form-item
        label="广告主"
        prop="posterEnId"
        :rules="[
          {required:true,message: '广告主不能为空'}
        ]"
      >
        <el-select
          size="small"
          v-model="form.posterEnId"
          filterable
          :disabled="disabled"
          placeholder="请选择广告主"
          :filter-method="enterpriceChange"
        >
          <el-option
            v-for="item in posterList"
            :key="item.enId"
            :label="item.enName"
            :value="item.enId"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item
        label="活动名称"
        prop="activityName"
        :rules="[
          {required:true,max:10,message: '活动名称不能为空或不符合规则'}
        ]"
      >
        <el-input data-type="input" :maxlength="10" size="small" v-model="form.activityName"></el-input>
        <div>
          <label>
            <input type="checkbox" :checked="form.display" @change="checkboxChange" />
            活动名称在红包素材上显示
          </label>
        </div>
      </el-form-item>

      <el-form-item
        label="活动时间"
        prop="date"
        :rules="[
          {required:true,message: '活动名称不能为空'}
        ]"
      >
        <el-date-picker
          v-model="form.date"
          :clearable="true"
          type="daterange"
          size="small"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>

      <el-form-item
        label="活动份数"
        prop="count"
        :rules="[
          {required:true,pattern:/^\d+$/,message: '活动份数格式错误'}
        ]"
      >
        <el-input-number
          size="small"
          :min="1"
          :step="1"
          @change="calcPredictPrice"
          :max="999999999"
          v-model="form.count"
          :disabled="disabled"
        ></el-input-number>
      </el-form-item>

      <el-form-item
        label="单个红包金额"
        label-width="70"
        prop="redMoney"
        :rules="[
          {required:true,pattern:/^[\d]+\.?[\d]{0,2}$/,message: '最少0.1，最多99元,两位小数'}
        ]"
      >
        <el-input-number
          :disabled="disabled"
          v-model="form.redMoney"
          @change="calcPredictPrice"
          size="small"
          :min="0.1"
          :max="99"
          label="描述文字"
        >
        </el-input-number>
      </el-form-item>

      <el-form-item
        label="物业佣金比例"
        size="small"
        labelWidth="100px"
        prop="propertyRatio"
      >
        <el-input-number
          size="small"
          :disabled="disabled"
          v-model="form.propertyRatio"
          @change="calcPredictPrice"
          placeholder="按单个红包金额比例填写"
          :min="0"
          :max="99"
          label="描述文字"
        >
        </el-input-number>
        %
      </el-form-item>

      <el-form-item
        label="平台抽佣比例"
        prop="platformRatio"
        labelWidth="100px"
      >
        <el-input-number
          size="small"
          :disabled="disabled"
          v-model="form.platformRatio"
          @change="calcPredictPrice"
          placeholder="按单个红包金额比例填写"
          :min="0"
          :max="99"
          label="描述文字"
        >
        </el-input-number>
        %
      </el-form-item>

      <el-form-item
        label="预存金额"
        prop="prestoreMoney"
      >
        <span> {{form.prestoreMoney}} </span>
      </el-form-item>

      <el-form-item
        label="活动素材"
        prop="prestoreMoney"
      >
        <el-upload
          list-type="picture"
          ref="upload"
          :action="uploadHost"
          accept=".jpg,.png"
          :before-upload="testUploadFile"
          :on-success="materialUploadSuccess"
          :on-remove="materialUploadRemove"
          :limit="1"
          :file-list="file"
          :on-exceed="()=>{$message('最多上传一张素材')}"
        >
          <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png，725*188像素格式图片</div>
          </el-upload>

        <div>

        </div>
      </el-form-item>

      <el-form-item
        label="活动内容"
      >
        <div id="editorElem" style="text-align:left"></div>
      </el-form-item>

      <footer class="add-actively-footer">

        <router-link to="/made_actively" tag="span">
          <el-button>
            返回
          </el-button>
        </router-link>

        <el-button type="primary" @click="prelook">
          预览
        </el-button>

        <el-button type="primary" @click="submit">
          保存
        </el-button>
      </footer>
    </el-form>

    <el-dialog
      :visible="preDiaLogVisible"
      title="预览"
      :before-close="cancelPreLook"
    >
      <div class="add-actively-prelook">
        <div v-html="editLookHtml">

        </div>
      </div>
      <!--<span slot="footer" class="dialog-footer">-->
        <!--<el-button type="primary" @click="preDiaLogVisible = false">确 定</el-button>-->
      <!--</span>-->
    </el-dialog>
  </div>
</template>

<script>

  import E from 'wangeditor';

  import omit from '@/utils/omit';

  import httpUploadFile from '@/utils/httpUploadFile';

  import util from '@/utils/util';

  import moment from 'moment';

  export default{

    props:{
      type:{type:String,default:"add"},
      data:{type:Object,default:null },
      id:{ type:String,default:null }
    },

    data(){
      return {
        preDiaLogVisible:false,  //预览弹出框
        editLookHtml:"",  //预览弹出框内容
        uploadHost:"http://120.78.12.39:80/file/uploadfile",
        file:[], //素材文件
        posterList:[], //广告主列表
        editFileList: [], //编辑器文件
        disabled:false,
        form:{
          display:true, //是否显示广告名称
          posterEnId:"", //广告主id
          activityName:"", //活动名称
          count:1,  //活动份数
          redMoney:1,  //红包金额
          propertyRatio:0,  //物业比例
          platformRatio:0,  //平台比例
          date:[],  //时间 -- 包括开始结束时间
          prestoreMoney:"", //预存金额
        }
      }
    },

    methods:{
      prelook(){
        this.preDiaLogVisible = true;
        this.editLookHtml = this.editor.txt.html();
      },
      checkboxChange(e){
        this.form.display = e.target.checked;
      },
      cancelPreLook(){ this.preDiaLogVisible = false },
      editFileUpload(){
        return new Promise((resolve,reject)=>{
          const files = this.editFileList;
          if (files.length >= 1) {
            httpUploadFile(files,(status,mes) => {
              if(status === "success"){
                resolve();
              } else {
                reject();
              }
            })
          } else {
              reject('empty')
          }
        })
      },
      testImgSize(base64,{width,height}){
        return new Promise((res,rej)=>{
            const Img = new Image();
            Img.onload = ()=>{
              if(Img.width === width && Img.height === height){
                  res("ok");
              }else{
                  rej("文件大小不符合规则")
              }
            };
            Img.onerror = (err)=>{ rej(err) };
            Img.src = base64;
        })
      },

      update(data){

        const form = this.form;

        if (this.type === "edit") {

          //获取广告主列表
          this.getEnterpriceList("");

          const {startDate, endDate, posterEnId, activityName, count, redMoney, display, propertyRatio, platformRatio, activityDesc} = data;

          let fodderURL = data.fodderURL ? JSON.parse(data.fodderURL) : "";

          form.date = [moment(startDate)._d, moment(endDate)._d];
          form.posterEnId = posterEnId;
          form.activityName = activityName;
          form.display = !display;  //取反 0 - 勾选 , 1 - 不勾选
          form.count = count;
          form.redMoney = redMoney;
          form.propertyRatio = propertyRatio ? propertyRatio * 100 : form.propertyRatio;
          form.platformRatio = platformRatio ? platformRatio * 100 : form.platformRatio;

          this.file = [{
            name: fodderURL.name,
            url: fodderURL.url,
            status: 'finished'
          }]

          this.disabled = true;

          this.calcPredictPrice(); /* 计算 */

          this.editor.txt.html(decodeURIComponent(activityDesc));

        }
      },

      getEnterpriceList(enName){
          this.$http.get('/enterprise/list', {
            params: {
              status: 0,
              pageNum: 1,
              pageSize: 10000,
              enName:enName || ""
            }
          })
            .then((res)=>{
              if (res.errorCode === '0') {
                this.posterList = res.resultData.enterprise || [];
              }
            })
      },
      enterpriceChange(value){
        if(value){
          //lodash 间隔加载
          this.getEnterpriceList(value);
        }
      },

      testUploadFile(file){

        const type = ["image/png","image/jpg","image/jpeg"];

        if(type.indexOf(file.type) === -1){
          this.$message("请检查文件格式");
          return false;
        }
      },

      materialUploadSuccess(respnse,file,fileList){
        const reader = new FileReader();
//      725 , 188
        const accept = {width:725,height:188};
        reader.addEventListener("load",()=>{
          this.testImgSize(reader.result,accept).then((status)=>{
            const res = file.response = JSON.parse(decodeURIComponent(file.response));
            if(res.status === "0"){
              this.file = [file];
            }else{
              this.$message("图片上传失败,请重新上传");
            }
          }).catch((err)=>{
            this.file = [];
            this.$message(err);
          })
        });
        reader.readAsDataURL(file.raw);
      },

      materialUploadRemove(file,fileList){
        this.file = fileList;
      },

      /**
       * 计算
       */
      calcPredictPrice(){
        this.$nextTick(()=>{
          const { count,redMoney,propertyRatio,platformRatio } = this.form;
          if(count && redMoney){
            this.form.prestoreMoney = (count * (redMoney + redMoney*((propertyRatio + platformRatio)/100))).toFixed(2);
          }
        })
      },
      dataURLtoBlob(dataurl) {
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        return new Blob([u8arr], { type: mime });
      },

      submit(){
        let form = this.form,filterForm
        const {propertyRatio,platformRatio} = filterForm = omit(form,["date"]);

        filterForm.propertyRatio = filterForm.propertyRatio / 100
        filterForm.platformRatio = filterForm.platformRatio / 100
        filterForm.display = filterForm.display ? 0 : 1

        const type = this.$props.type;
        const file = this.file;
        if(propertyRatio + platformRatio > 100){
            this.$message("物业佣金比例平台佣金比例和不能大于100");
            return;
        }
        if(file.length < 1){ this.$message("请上传活动素材"); return; }
        this.$refs.form.validate((valid) => {
          if (!valid) { this.$message("请检查格式"); return; }

          const editHtml = this.editor.txt.html();
          const fileInfo = file[0].response || file[0];

          console.log(editHtml);

          let data = {
            ...filterForm,
            fodderURL:JSON.stringify(fileInfo),
            startDate: moment(form.date[0].valueOf()).format("YYYY-MM-DD"),
            endDate: moment(form.date[1].valueOf()).format("YYYY-MM-DD"),
            activityDesc:encodeURIComponent(editHtml),
          };

          if(type === "edit"){ data.id = this.$props.id; }

          this.$http.post("/activity/save", data).then((res) => {
            if (res.errorCode === "0") {
              this.$message({
                type: "success",
                message: "保存成功",
              })
              this.$router.replace("/made_actively")
            }
          })
        })
      },
      editUploadImgChange(){

      }
    },

    created(){

      this.type = this.$props.type;

      this.listenEditUoloadImg = {
        customInsert: function (insertImg, result, editor) {
          var url = result.url
          insertImg(url)
        },
        success: function (xhr, editor, result) {
        },
      }
    },

    mounted(){

      const editor = this.editor = new E('#editorElem');

      const from = this.form;

      const _this = this;

      if(this.type !== "edit"){  /* 如果是编辑进时不计算 */
        this.calcPredictPrice();  //计算预计金额
      }

      // 配置服务器端地址
      editor.customConfig.uploadImgServer = this.uploadHost;
      editor.customConfig.onChange = this.editUploadImgChange;
      editor.customConfig.uploadImgHooks = this.listenEditUoloadImg;
      editor.customConfig.customAlert = function (info) {
        // info 是需要提示的内容
        alert('自定义提示：' + info)
      };
      editor.customConfig.zIndex = 1000;

      //自定义上传
      editor.customConfig.customUploadImg = function (files, insert) {

          let fileList = [];

          httpUploadFile(files,(status,res)=>{
              if (status === 'success') {
                insert(JSON.parse(res)[0].url)
              } else {
                _this.$message({type:"error",message:'上传图片失败'});
              }
          })
//          files.forEach((file,k)=>{
//            util.getBase64(file).then(({status,result,error})=>{
//              if(status === 'done'){
//                insert(result)
//                fileList.push(file)
//                if (k === files.length - 1) { /* 最后一个 */
//                    _this.editFileList = fileList
//                }
//              }
//            }).catch(({error})=>{
//              _this.$message({type:"error",message:error})
//            })
//          })

      };

      editor.create();

    }
  }

</script>

<style>
  .add-actively{
    .el-dialog{
      width: 420px !important;
    }
    h1{
      font-size: 1.6em;
    }
    :global(.el-upload-list__item-thumbnail){   width: 350px;   }
  }
  .add-actively-form div[data-type="input"]{
    width: 500px !important;
  }
  .add-actively-footer{
    text-align: center;
  }
  .add-actively-prelook{
    position: relative;
    width: 328px;
    height: 590px;
    margin: 0 auto;
    background: url("../../../../assets/download-2.png") no-repeat 4px top;
    padding-top: 84px;
    &::before{
       content:"";
       display: table;
    }

     >div{
        width: 284px;
        height: 501px;
        margin: 0 auto;
        background: #fff;
        overflow: scroll;
        padding: 0 10px;
        box-sizing: border-box;
     }
  }

</style>



