<template>
  <div id="login">
    <div class="loginForm">
      <div class="login-title">
        <img src="../assets/logo.png" alt="">
        <h2 v-if="type === 1">广告主平台</h2>
        <h2 v-else-if="type === 2">物业端平台</h2>
        <h2 v-else>广告管理平台</h2>
      </div>

      <el-form
        :model="ruleForm2"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="50px"
        class="demo-ruleForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="账号" prop="name">
          <el-input v-model.number="ruleForm2.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" @keyup.enter.native="Enter" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <br>
        <el-form-item>
          <el-button class="submitBtn" type="primary" @click="submitForm('ruleForm2')">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data () {
      var checkName = (rule, value, callback) => {
        if (!value || !(/^1[3|4|5|6|7,8]\d{9}$/.test(value))) {
          return callback(new Error('手机号输入有误'))
        } else {
          callback()
        }
      }
      var validatePass = (rule, value, callback) => {
        if (!value || value.length < 6 || value.length > 20) {
          callback(new Error('密码输入有误'))
        } else {
          callback()
        }
      }
      return {
        ruleForm2: {
          pass: '',
          name: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'change' }
          ],
          name: [
            { validator: checkName, trigger: 'change' }
          ]
        },
        type: this.$router.history.current.meta.loginType,
        r: {
          '1': '/districtManagement',
          '2': '/advertiserManagement',
          '3': '/made_actively',
          '4': '/userManagement',
          '5': '/rechargeRecord',
          '6': '/opinionFeedback',
          '7': '/propertyPresentAudit',
          '8': '/enterpriseAccount',
          '9': '/permissionManagement',
          '10': '/propertyManagement',
          '11': '/callSetting',
          '12': '/trafficSetting',
        }
      }
    },
    methods: {
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.Login()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      Login () {
        let _this = this
        this.$http.get('/backuser/login', {
          params: {
            type: this.type || 0, // 0 后台管理，1 广告主， 2 物业
            account: this.ruleForm2.name,
            password: this.ruleForm2.pass
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {

              res.resultData.backuser.token = res.token;
              res.resultData.backuser.enId = res.resultData.enId;

              /* 圆括号前必须加分号，保证与其他语句不冲突 */
              ;(_this.type === 1 || _this.type === 2) && sessionStorage.setItem('enterpriseName', res.resultData.EnterpriseName);

              sessionStorage.setItem('userInfo', JSON.stringify(res.resultData.backuser))

              if (_this.type === 0) {

                let pidsArr = res.resultData.backuser.pids.split(',')[0]

                _this.$router.push({
                  path: _this.r[pidsArr]
                })
              } else {
                _this.$router.push({
                  path: '/'
                })
              }

              window.location.reload()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      Enter() {
        this.submitForm('ruleForm2')
      }

    }

  }
</script>

<style scoped>
  #login{
    height: 100vh;
    display: flex;
  }
  .loginForm {
    margin: auto;
    width: 440px;
    padding: 30px 20px;
    border-radius: 5px;
    border: 1px solid #ededed;
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
    -webkit-transition: .3s all;
    transition: .3s all;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    z-index: 10;
    background: #fff;
  }
  .login-title{
    display: flex;
    margin-bottom:1em;
    justify-content: center;
    align-items: center;
    > img{
        width: 50px;
        height: 50px;
        margin-right: 10px;
    }
    h2{
      margin:0;
    }
  }
  .loginForm >p {
    line-height: 24px;
    text-align: center;
    font-size: 22px;
    color: #409eff;
  }
  .submitBtn {
    float: right;
  }
</style>



