<template>
  <div class="made-activity-detail">
    <header class="made-activity-detail__header">
      <h3 class="title"> {{`${data.activityName} (${data.posterEnName})`}} </h3>
      <div class="header-main">
        <span>投放时间 : {{ `${data.startDate} 至 ${data.endDate}`  }}</span>
        <ul class="list">
          <li v-for="(item,key) in table_menu" :key="key">
            <strong> {{ key }} </strong>
            <span> {{ data[item] }} </span>
          </li>
        </ul>
      </div>
    </header>

    <section class="made-activity-detail__section">

      <el-button-group class="made-activity-detail__tableHeader">
        <el-button @click="toggleStatus(1,$event)" size="small" :type="status === 1 ? 'primary' : 'info' ">汇总</el-button>
        <el-button @click="toggleStatus(2,$event)" size="small" :type="status === 2 ? 'primary' : 'info' ">领取明细</el-button>
      </el-button-group>

      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
      >
        <el-table-column
          v-for="(item,key) in columns"
          :key="key"
          :prop="item.prop"
          :label="item.label"
        >
        </el-table-column>
      </el-table>

    </section>

    <footer class="made-activity-detail__footer" v-show="isPagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </footer>

  </div>
</template>

<script>

  import _ from 'lodash';

  export default{

    created(){
      this.id = _.get(this,"$route.params.id");
      this.columns = this.columns_1;
    },

    data(){
      return {
        total:0,
        isPagination:false,  //只有详情时，显示分业
        currentPage:1,
        status:1,
        data:{},
        tableData:[],
        columns_1:[
          {label:"投放小区",prop:"cName"},
          {label:"领取人数",prop:"count"},
          {label:"单个红包金额（元）",prop:"redMoney"},
          {label:"物业佣金（元）",prop:"propertyMoney"},
          {label:"平台抽佣（元）",prop:"platformMoney"},
        ],
        columns_2:[
          {label:"用户昵称",prop:"nickname"},
          {label:"手机号",prop:"phone"},
          {label:"领取时间",prop:"createTime"}
        ],
        columns:[],
        table_menu:{
            "活动份数":"count",
            "已领取":"already_get",
            "预存金额":"prestoreMoney",
            "剩余金额":"surplusMoney",
        }
      }
    },

    watch:{
      status(value){
          if(value === 2){ this.isPagination = true; }else{
            this.isPagination = false;
          }
      }
    },

    mounted(){
      if (this.id) {
        this.columns = this.columns_1;
        //获取列表
        this.reqList(1,"all");
      }
    },

    methods:{
        /* type {String} all || detail */
        reqList(pageNum,type){

          pageNum = pageNum || 1;

          if(type === "all"){
            //获取头部信息
            this.$http.get("/activitystatistics/activity", {
              params: {
                activityId: this.id
              }
            }).then((res) => {

              const {activityCollect,activity} = res.resultData;

              this.tableData = activityCollect;

              activity.already_get = activity.count - activity.surplusCount;

              this.data = activity;

            })
          }else{
            this.$http.get("/activitystatistics/activityDetail",{
              params:{
                activityId:this.id,
                pageNum:pageNum || 1,
                pageSize:10,
              }
            }).then((res)=>{
              const {total,accmList} = res.resultData;
              accmList.forEach((i,k)=>{
                  i.nickname = decodeURIComponent(i.nickname)
                  i.createTime = new Date(i.createTime).toLocaleDateString();
                  i.phone = i.phone || "无"
              });
              this.total = total;
              this.tableData = accmList || [];
            })
          }

        },
        toggleStatus(type){
          this.status = type;
          this.columns = this[`columns_${type}`];
          this.reqList(1,type === 1 ? 'all' : 'detail')
        },
        handleCurrentChange(page,pageNum){
          this.reqList(pageNum,"detail")
        }
    }
  }

</script>

<style scoped>
  .title{
    margin-bottom: 15px;
    font-size:23px;
  }
  .list{
    display: flex;
    margin-top:5px;
    li{
      margin-right:30px;
    }
  }
  .header-main{
    font-size: 14px;
  }
  .made-activity-detail__header{
    margin-bottom: 30px;
  }

  .made-activity-detail__section{
    margin-top: 15px;
  }

  .made-activity-detail__footer{
    text-align: right;
    margin-top: 15px;
  }

  .made-activity-detail__tableHeader{
    margin-bottom:10px;
  }
</style>



