<template>
  <div id="advertiserManagement">
    <div class="searchModule">
      <span>广告主</span>
      <el-input size="small" v-model="enName" placeholder="请输入内容"></el-input>
      <span>状态</span>
      <el-select size="small" v-model="value" placeholder="全部">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button size="small" type="primary" @click="search"> 搜索</el-button>
      <el-button
        size="small"
        class="searchModule_add"
        icon="el-icon-plus"
        type="primary"
        @click="handleEdit"
      >
        新增广告主
      </el-button>
    </div>
    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        prop="enCode"
        label="广告主ID">
      </el-table-column>
      <el-table-column
        prop="enName"
        label="广告主">
      </el-table-column>
      <el-table-column
        prop="contactUser"
        label="负责人">
      </el-table-column>
      <el-table-column
        prop="contactPhone"
        label="联系方式">
      </el-table-column>
      <!--<el-table-column-->
        <!--prop="investMoney"-->
        <!--label="投放余额（元）">-->
      <!--</el-table-column>-->
      <el-table-column
        prop="status"
        label="状态">
        <template slot-scope="scope">
          <span v-if="scope.row.status === 0">正常</span>
          <span v-else>禁用</span>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        prop="address"
        label="操作"
        width="100"
      >
        <template slot-scope="scope">
          <a @click="handleUpdate(scope.$index, scope.row.enId, '1')" v-if="scope.row.status === 0">禁用</a>
          <a @click="handleUpdate(scope.$index, scope.row.enId, '0')" v-else>启用</a>&nbsp;
          <a @click="handleEdit(scope.$index, scope.row)">编辑</a>&nbsp;
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <span v-if="updateStatus === '0'">确定要启用吗？</span>
      <span v-else>确定要禁用吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmUpdate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'advertiserManagement',
    data () {
      return {
        dialogVisible: false,
        enName: '',
        pageNum: 1,
        total: 0,
        tableData: [],
        updateEnId: '',
        updateStatus: '0',
        deleteUid: null,
        options: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '正常'
        }, {
          value: '1',
          label: '禁用'
        }],
        value: '',
        lastWord: '',
        lastValue: ''
      }
    },
    mounted () {
      this.advertiserList()
    },
    methods: {
      search () {
        this.pageNum = 1
        this.lastWord = this.enName
        this.lastValue = this.value
        this.advertiserList()
      },
      advertiserList () {
        let _this = this
        this.$http.get('/enterprise/list', {
          params: {
            status: this.value,
            pageNum: this.pageNum,
            pageSize: 10,
            enName: this.enName
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.enterprise
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleEdit (index, row) {
        row ? (
          this.$router.push({
            path: '/addAdvertiser',
            query: {
              enId: row.enId
            }
          })
        ) : (
          this.$router.push('/addAdvertiser')
        )
      },
      handleUpdate (index, enId, status) {
        this.dialogVisible = true
        this.updateEnId = enId
        this.updateStatus = status
        return this
      },
      confirmUpdate () {
        let _this = this
        this.$http.get('/enterprise/update/status', {
          params: {
            enId: this.updateEnId,
            status: this.updateStatus
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message.success({
                message: '修改成功'
              })
              _this.dialogVisible = false
              _this.advertiserList()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.enName = this.lastWord
        this.value = this.lastValue
        this.advertiserList()
      }
    }
  }
</script>

<style>
  .searchModule {
    margin-bottom: 20px;
  }
  .searchModule span {
    margin-right: 6px;
  }
  .searchModule > div {
    width: 200px;
    margin-right: 6px;
  }
  .searchModule_add {
    float: right;
  }
</style>



