import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login'

import AdvertiserResult from '../pages/advertiserResult.vue'
import GetDetail from '../pages/getDetail.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/advertiserResult',
      alias:"/",
      name: '广告效果',
      component: AdvertiserResult,
      meta: { requiresAuth: true }
    },
    {
      path: '/getDetail/:id',
      name: '领取明细',
      component: GetDetail,
      meta: { requiresAuth: true }
    }
  ]
})

export default router



