<template>
  <div class="made-actively">
    <header class="clear">
      <div class="left">

        <div class="left" :style="{marginRight:'25px'}">
          <el-select size="small" v-model="searchType" placeholder="请选择">
            <el-option
              v-for="item in searchOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <el-input :style="{width:'100px'}" v-model="input" size="small" placeholder="请输入内容" >
          </el-input>
        </div>

        <div class="left">
          <el-select size="small" :style="{width:'100px'}" v-model="statusValue" placeholder="请选择">
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <el-button type="primary" size="small" :style="{marginLeft:'10px'}" @click="headerSearch"> 查询 </el-button>
      </div>

      <div class="right">
        <router-link to="/made_actively_add" tag="span">
          <el-button type="primary" size="small">
            <span> 新增 </span>
          </el-button>
        </router-link>
        <el-button type="primary" size="small" @click="handleActivily('stop')"> 活动停止 </el-button>
        <el-button type="primary" size="small" @click="handleActivily('start')"> 活动开启 </el-button>
      </div>

    </header>

    <section class="made-actively-section">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >

        <el-table-column
          type="selection"
          width="55"
        >
        </el-table-column>

        <el-table-column
          v-for="(item,key) in columns"
          :key="key"
          :prop="item.prop"
          :label="item.label"
        >
        </el-table-column>

        <el-table-column
          label="投放小区数量"
          prop="communityCount"
        >
          <template slot-scope="scope">
            <a href="" @click.prevent="lookHouseList(scope.row)" v-show="scope.row.status !== 0"> {{scope.row.communityCount}} </a>
          </template>
        </el-table-column>

        <el-table-column
          label="状态"
          width="150"
          prop="status"
        >
          <template slot-scope="scope">
            <span> {{

                scope.row.status === 0 ? "待投放" :
                scope.row.status === 1 ? "投放中" : "投放结束"

              }} </span>
          </template>
        </el-table-column>

        <el-table-column
          label="操作"
          width="150">
          <template slot-scope="scope">

            <el-button @click="madeQrcode(scope.row)" type="text" size="small">
              {{scope.row.status === 0 ? "生成二维码" : "效果明细"}}
            </el-button>

            <router-link :to="`/made_actively_edit/${scope.row.id}`">
              <el-button type="text" size="small"> 编辑 </el-button>
            </router-link>

          </template>
        </el-table-column>

      </el-table>

      <footer class="made-actively-footer">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </footer>

      <el-dialog
        :title="dialogTitle"
        width="30%"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        class="made-actively-dialog"
      >
        <div ref="dialogWrap">
          <div class="made-actively-dialog-item" v-for="(item,key) in houselist"  :key="key">
            <div class="made-actively__qrcodeInput">
              <span> 投放小区 </span>
              <el-select
                size="small"
                :filterable="true"
                v-if="dialogTitle === '生成二维码' "
                v-model="item.communityCid"
                @change="(value)=>{communityChange(value,key)}"
                popper-class="made-actively-dialog__select"
              >
                <el-option
                  v-for="item in communityList"
                  :key="item.cid"
                  :value="item.cid"
                  :label="item.cName"
                >
                  <strong class="made-actively-dialog__cName">
                    {{ item.cName }}
                  </strong>
                  <span>
                    {{`${item.province}${item.province}${item.district}${item.address}`}}
                  </span>
                </el-option>
              </el-select>

              <span v-else> {{item.communityCname}} </span>

            </div>

            <div class="made-actively__qrcode---wrap" v-for="(qrCodeSrc,_key) in item.qrCodeSrcs">
              <div class="made-actively__qrcode">
                <img :src="qrCodeSrc" alt="" />
              </div>
              <div class="made-actively__qrcode---handle">
                <el-button size="small"
                           @click="removeQrcode(item,key,_key)" v-if="dialogTitle === '生成二维码' ">删除</el-button>
                <el-button size="small" type="primary" @click="handleQrcode(item,_key)">
                  {{ qrCodeSrc === defaultImg ? "生成二维码" : "下载二维码" }}
                </el-button>
              </div>
            </div>
          </div>
        </div>

        <el-button size="small" v-show="dialogTitle === '生成二维码' " @click="addHouse"> 增加投放小区 </el-button>

        <div slot="footer" class="dialog-footer" v-show="dialogTitle !== '查看二维码'">
          <el-button @click="handleClose">取 消</el-button>
          <el-button type="primary" @click="houseSubmit">确认投放</el-button>
        </div>
      </el-dialog>
    </section>

  </div>
</template>

<script>

  import { Loading } from 'element-ui';

  export default{

    data(){
      return {
        host:"http://120.78.12.39:80",
        dialogVisible:false, //二维码弹出框
        input:"",       //查询的内容
        statusValue:"",   //查询时的状态
        searchType:"enName", //查询时的类型
        multipleSelected:[],  //table多选框选中的cid
        total:0,  //总条数
        currentPage:1,  //当前页数


        //弹出层
        dialogLoading:false,
        dialogTitle:null,
        communityList:[],   //投放的小区数据列表
        defaultImg:"http://120.78.12.39:80/file/download?filename=/opt/poster/upload/uploadfile/1510827267982.jpg",
        // look时，会添加两个字段 (communityCname, address){String}
        houselist:[{communityCid:null,qrCodeSrcs:["http://120.78.12.39:80/file/download?filename=/opt/poster/upload/uploadfile/1510827267982.jpg"]}],  //投放小区view列表
        currentHandleActivityId:null, //当前操作的活动id，点击刷新.
        wid:null, //物业id，选择投放小区时更新

        columns:[
          {label:"活动ID",prop:"id"},
          {label:"活动名称",prop:"activityName"},
          {label:"投放时间",prop:"startDate"},
          {label:"广告主",prop:"posterEnName"},
          {label:"活动份数",prop:"count"},
          {label:"预存金额(元)",prop:"prestoreMoney"},
          {label:"剩余金额(元)",prop:"surplusMoney"},
        ],
        tableData:[],
        statusOptions:[
            {value:"",label:"全部"},
            {value:"0",label:"待投放"},
            {value:"1",label:"投放中"},
            {value:"2",label:"投放结束"},
        ],
        searchOptions:[
          {value: "enName",label:"广告主"},
          {value: "activityName",label:"活动名称"},
        ]
      }
    },
    mounted(){
      this.reqList({ pageNum:1 });
    },
    methods:{
      /* //0 待投放  1 投放中  2  投放结束 */
      reqList(param){

        const {pageNum,status,activityName,enName} = param;

        const pa = {
          pageSize:10,
          pageNum: pageNum || 1,
          status:status || "",
          activityName: activityName || "",
          enName: enName || ""
        };

        this.$http.get("/activity/list",{
          params:pa
        }).then((res)=>{
          this.total = res.resultData.total;

          this.tableData = res.resultData.activity;
        });
      },
      handleCurrentChange(page){
          this.currentPage = page;

          if(this.searched){

            const {input,statusValue,searchType,currentPage} = this;

            this.reqList({ pageNum:page,[searchType]:input, status:statusValue, currentPage });

          }else{ this.reqList({pageNum:page}); }

      },
      handleSelectionChange(val){
        this.multipleSelected = val;
      },
      handleClose(){
        this.clear();
        this.dialogVisible = false;
      },
      communityChange(value,key,cb){

        this.wid = null;

        if(key){
          //切换时清空,二维码
          this.houselist[key].qrCodeSrcs = [this.defaultImg];
        }

        this.$http.post("/community/query/property",{  /* 更新物业id */
          cid:value,
        }).then((res)=>{
          let propertyIds = [];
          res.resultData.relations.forEach((i)=>{ propertyIds.push(i.propertyId) });
          this.wid = propertyIds;
          cb && cb();
        })
      },
      houseSubmit(){ /* 确认投放 */

        let empty,cids = [];

        const {houselist,currentHandleActivityId} = this;

        houselist.forEach((i)=>{
          if(!i.communityCid || i.qrCodeSrcs[0] === this.defaultImg){ empty = true } else{
                cids.push(i.communityCid);
            }
        });

        if(houselist.length <= 0){
          this.$message({
            type:"error",
            message:"最少也得添加一个投放小区吧",
          }); return;
        }

        if(empty || !currentHandleActivityId || cids.length <= 0){
          this.$message({ type:"error",message:"请把投放小区填写完整", });
          return;
        };


        this.$http.post("/activity/save/throwInfor",{
          id:currentHandleActivityId,
          cids:cids.join(","),
        }).then((res)=>{
          this.$message({ type:"success",  message:"投放成功" });
          this.handleClose();
          this.reqList({pageNum:this.currentPage});
        })
      },

      headerSearch(){

          const {input,statusValue,searchType,currentPage} = this;

          this.searched = true;

          this.reqList({ [searchType]:input, pageNum:1, status:statusValue, currentPage });

      },
      removeQrcode(item,key,_key){

        if(item.qrCodeSrcs.length > 1){  /* 说明是最后一个qrcode */
              item.qrCodeSrcs.splice(_key,1);
          }else{
            this.houselist.splice(key,1);
          }
      },
      clear(){
          requestAnimationFrame(()=>{
            this.communityList = [];   //投放的小区数据列表
            this.houselist = [{communityCid:null,qrCodeSrcs:[this.defaultImg] }];  //投放小区view列表
            this.currentHandleActivityId = null; //当前操作的活动id，点击刷新.
            this.onceSelected = false;
          },2000);
      },
      addHouse(){
          this.houselist.push({communityCid:null,qrCodeSrcs:[this.defaultImg] });
      },
      lookHouseList(row){

        const {id} = row;

        this.dialogTitle = "查看二维码";

        this.dialogVisible = true;

        let loading;

        setTimeout(()=>{
          loading =  this.$loading({ target:this.$refs.dialogWrap, });
        });

        this.$http.get("/activity/list/throwInfor", { params: { id } }).then((res) => {

          res = res.resultData.communities;

          let list = [];

          const updateView = ()=>{ this.houselist = list; };

          if(res.length === 0 ){
              loading.close();
              this.houselist = [];
          }

          res.forEach((i,k)=>{

            let srcs = [];

            this.communityChange(i.cid,null,()=>{

                srcs = this.createQrCodes(this.wid,id,i.cid);

                list.push({
                  communityCid: i.cid,
                  communityCname: i.cName,
                  qrCodeSrcs: srcs,
                  address:`${i.province}${i.city}${i.district}${i.address}`
                });

                if(res.length-1 === k){
                  loading.close();
                }

                updateView();
            })

          });

        });

      },
      handleActivily(type){

        let ids = [], url

        this.multipleSelected.forEach((i)=>{ ids.push(i.id) })

        if (ids.length < 1) {
          this.$message({
            message:"没有可操作的活动哦！",
            type:"error",
          });

          return;
        }

        if (type === 'start') {
            url = '/activity/stop';
        } else {
            url = '/activity/start';
        }

        this.$http.post(url,{ ids:ids.join(",") }).then(res => {

          this.$message({
            message:`活动${type === 'start' ? '开启' : '停用'}成功`,
            type:"success",
          });

          this.reqList({ pageNum:this.currentPage });

        })
      },
      madeQrcode(item){

        if(item.status !== 0){  /*效果明细*/
            this.$router.push({
              path:`made_actively_detail/${item.id}`,
            }); return
        }

        this.dialogTitle = "生成二维码";

        //请求投放小区
        this.$http.get("/community/list",{
            params:{
              pageNum:1,
              pageSize:10000000,
              search:"",
              status:0
            }
        }).then((res)=>{
          this.communityList = res.resultData.community;
        });

        this.dialogVisible = true;
        this.currentHandleActivityId = item.id;
      },

      handleQrcode(item,_key){

        const {currentHandleActivityId} = this

        const {communityCid} = item

        const _item = item.qrCodeSrcs[_key];

        let cid

        //没有物业id或wid未加载
        if(!communityCid || !this.wid){
          this.$message({
            showClose: true,
            message:'错了哦，请选择投放小区',
            type:'error'
          });
          return;
        }

        if(_item !== this.defaultImg){  /* 为下载二维码 */
          const a = document.createElement("a");
          a.href = _item;
          a.setAttribute("download", "");
          a.style.display = "none";
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          return;
        }

        if(communityCid && currentHandleActivityId){
            item.qrCodeSrcs = this.createQrCodes(this.wid,currentHandleActivityId,communityCid);
        }
      },

      createQrCodes(wids,id,cid){
          let qrCodes = [];

          if(!wids){ return ; }

          wids.forEach((i,k)=>{
            qrCodes.push(`${this.host}/other/qrCode?activityId=${id}&cid=${cid}&propertyId=${i}`);
          });

          if(qrCodes.length === 0 ){
            qrCodes.push(`${this.host}/other/qrCode?activityId=${id}&cid=${cid}&propertyId=0`);
          }

          return qrCodes;
      }
    }
  }

</script>

<style>
  .made-actively-section{
    margin-top:30px;
  }

  .made-actively-dialog{
    .el-dialog{
      width:650px !important
    }
  }

  .made-actively-section .el-form-item__content{
    width:80px;
  }

  .made-actively-footer{
    text-align: right;
    margin-top: 30px;
  }

  .made-actively-dialog-item{
    margin-bottom: 40px;
  }

  .made-actively-dialog__select{
    .el-select-dropdown__item{
      height:auto;
    }
  }

  .made-actively-dialog__cName{
    display: block;
  }

  .made-actively__qrcode---wrap{   margin-top: 10px;  }

  .made-actively__qrcode{
    display: inline-block;
    width: 250px;
    height: 250px;
    margin-right: 15px;

    >img{
       width: 100%;
       height: 100%;
    }
  }
  .made-actively__qrcode---handle{
    display: inline-block;
    vertical-align: bottom;
  }

  .made-actively-dialog__cName{
    display: block;
    line-height: 11px;
    margin-top: 12px;
  }
</style>



