<template>
  <div id="rechargeRecord">
    <div class="searchModule">
      <span>充值时间</span>
      <el-date-picker
        v-model="startDate"
        type="date"
        size="small"
        placeholder="开始日期"
        style="width: 160px;margin-bottom: 20px;"
        value-format="yyyy-MM-dd">
      </el-date-picker>至
      <el-date-picker
        v-model="endDate"
        type="date"
        size="small"
        style="width: 160px"
        placeholder="结束日期"
        value-format="yyyy-MM-dd">
      </el-date-picker>
      &nbsp;&nbsp;&nbsp;

      <el-select v-model="value" size="small" style="width: 130px" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <el-input size="small" style="width: 160px" v-if="value === '1'" v-model="nickname" placeholder="请输入内容"></el-input>
      <el-input size="small" style="width: 160px" v-else v-model="phone" placeholder="请输入内容"></el-input>

      <br>
      <span>充值类型</span>
      <el-select v-model="type" size="small" style="width: 140px" placeholder="全部">
        <el-option
          v-for="item in types"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <span>&nbsp;&nbsp;充值状态</span>
      <el-select v-model="status" size="small" style="width: 140px" placeholder="全部">
        <el-option
          v-for="item in statusOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <el-button size="small" type="primary" @click="search">搜索</el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
    </el-table>

    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import Util from '../../../utils/util'
  export default {
    name: 'rechargeRecord',
    data () {
      return {
        startDate: '',
        endDate: '',
        pageNum: 1,
        tableData: [],
        columns: [
          {prop: 'orderId', label: '订单编号'},
          {prop: 'serialNumber', label: '流水号'},
          {prop: 'payTime', label: '充值时间'},
          {prop: 'nickname', label: '用户昵称'},
          {prop: 'rechargePhone', label: '充值手机'},
          {prop: 'type', label: '充值类型'},
          {prop: 'payMoney', label: '充值金额（元）'},
          {prop: 'balance', label: '余额支付（元）'},
          {prop: 'wxPay', label: '微信支付'},
          {prop: 'status', label: '充值状态'}
        ],
        nickname: '',
        phone: '',
        options: [{
          value: '0',
          label: '充值手机'
        }, {
          value: '1',
          label: '用户昵称'
        }],
        value: '0',

        types: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '话费充值'
        }, {
          value: '1',
          label: '流量充值'
        }],
        type: '',

        statusOptions: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '充值中'
        }, {
          value: '1',
          label: '充值成功'
        }, {
          value: '-1',
          label: '充值失败'
        }],
        status: '',

        lastStatus: '',
        lastStartDate: '',
        lastEndDate: '',
        lastType: '',
        lastNickname: '',
        lastPhone: '',
        lastValue: '0',
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search () {
        this.lastStartDate = this.startDate
        this.lastEndDate = this.endDate
        this.lastType = this.type
        this.lastNickname = this.nickname
        this.lastPhone = this.phone
        this.lastValue = this.value
        this.lastStatus = this.status
        this.pageNum = 1
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.get('/pay/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            startDate: this.startDate,
            endDate: this.endDate,
            type: this.type,
            nickname: this.value === '1' ? this.nickname : '',
            phone: this.value === '0' ? this.phone : '',
            status: this.status
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let payRankList = res.resultData.payRankList || []
              payRankList.map((v, i)=>{
                v.payTime = Util.dateFormat(v.payTime)
                v.nickname = decodeURIComponent(v.nickname)
                v.nickname = v.nickname.replace(/\+/g, '')
                v.type === 0 ? v.type = '话费充值' : v.type =  '流量充值'
                v.status === '0' ? v.status = '充值中' : v.status === '1' ? v.status =  '充值成功' : v.status = '充值失败'
                return v
              })
              _this.tableData = payRankList
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.startDate = this.lastStartDate
        this.endDate = this.lastEndDate
        this.type = this.lastType
        this.nickname = this.lastNickname
        this.phone = this.lastPhone
        this.value = this.lastValue
        this.status = this.lastStatus
        this.getList()
      }
    }
  }
</script>

<style>

</style>



