<template>
  <div id="districtDetail" class="districtDetail">
    <div class="operatinBtn">
      <el-button type="primary" size="small" icon="el-icon-edit" @click="districtEdit">编辑</el-button>
      <el-button type="danger" size="small" icon="el-icon-delete" @click="dialogVisible = true">
        {{ districtDetail.status === 0 ? '禁用' : '启用'}}
      </el-button>
    </div>
    <ul>
      <li>
        <span>小区名称：</span>
        <p>{{ districtDetail.cName}}</p>
      </li>
      <li>
        <span>小区地址：</span>
        <p>{{ districtDetail.province + districtDetail.city + districtDetail.district + districtDetail.address  || '无' }}</p>
      </li>
      <li>
        <span>小区详情：</span>
        <p>{{ districtDetail.cDesc || '无' }}</p>
      </li>
      <li>
        <span>小区规模：</span>
        <p>{{ districtDetail.cScale || '无' }}</p>
      </li>
      <li>
        <span>物业：</span>
        <p>{{ districtDetail.property || '无' }}</p>
      </li>
      <li>
        <span>广告位数量：</span>
        <p>{{ districtDetail.posterNumber || '无' }}</p>
      </li>
      <li>
        <span>广告负责人：</span>
        <p>{{ districtDetail.principal || '无' }}</p>
      </li>
      <li>
        <span>负责人手机：</span>
        <p>{{ districtDetail.principalPhone || '无' }}</p>
      </li>
    </ul>

    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>{{ districtDetail.status === 0 ? '确定要禁用吗？' : '确定要启用吗？'}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteDistrict">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'districtDetail',
    data () {
      return {
        dialogVisible: false,
        districtId: this.$route.query.districtId,
        districtDetail: {}
      }
    },
    mounted () {
      this.get()
    },
    methods: {
      get () {
        let _this = this
        this.$http('/community/get', {
          params: {
            cid: this.districtId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.districtDetail = res.resultData.community
              console.log(res);
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      districtEdit () {
        this.$router.push({
          path: '/addDistrict',
          query: { districtId: this.districtId }
        })
      },
      deleteDistrict () {
        let _this = this
        this.$http('/community/delete', {
          params: {
            cid: this.districtId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '保存成功',
                type: 'success'
              })
              _this.dialogVisible = false
              _this.$router.push('/districtManagement')
              return _this
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style>
  .districtDetail .operatinBtn {
    float: right;
  }
  .districtDetail ul {
    padding: 0;
    clear: both;
  }
  .districtDetail ul li {
    list-style: none;
    line-height: 28px;
    clear: both;
  }
  .districtDetail ul li span {
    float: left;
    width: 100px;
    text-align: right;
  }
  .districtDetail ul li  p {
    margin-left: 100px;
  }
</style>



