<template>
  <div id="districtManagement">
    <div class="searchModule">
      <span>小区</span>
      <el-input size="small" v-model="searchName" placeholder="请输入内容"></el-input>
      <span>状态</span>
      <el-select v-model="value" size="small" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
      <el-button
        size="small"
        class="searchModule_add"
        icon="el-icon-plus"
        type="primary"
        @click="addDistrict"
      >
        新增小区
      </el-button>
    </div>
    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        prop="cid"
        label="小区ID">
      </el-table-column>
      <el-table-column
        prop="cName"
        label="小区名称">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
        <template slot-scope="scope">
          <span v-if="scope.row.city === '市辖区'">{{ scope.row.province + scope.row.district + scope.row.address }}</span>
          <span v-else>{{ scope.row.province + scope.row.city + scope.row.district + scope.row.address }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="property"
        label="物业"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.property.substr(0, scope.row.property.length - 1) }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="principal"
        label="广告负责人">
      </el-table-column>
      <el-table-column
        prop="posterNumber"
        label="广告位数量">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态">
        <template slot-scope="scope">
          <span>{{scope.row.status === 0 ? '正常' : '禁用'}}</span>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        prop="operation"
        label="操作"
        width="120"
      >
        <template slot-scope="scope">
          <a @click="handleDetail(scope.$index, scope.row)">查看详情</a>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'districtManagement',
    data () {
      return {
        searchName: '',
        pageNum: 1,
        tableData: [],
        lastWord: '',
        lastStatus: '',
        options: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '正常'
        }, {
          value: '1',
          label: '禁用'
        }],
        value: '',
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search () {
        this.pageNum = 1
        this.lastWord = this.searchName
        this.getList()
      },
      getList () {
        let _this = this
        this.$http('/community/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            search: this.searchName,
            status: this.value
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.community
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      addDistrict () {
        this.$router.push({
          path: '/addDistrict',
          query: { districtId: 0 }
        })
      },
      handleDetail (index, row) {
        this.$router.push({
          path: '/districtDetail',
          query: { districtId: row.cid }
        })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.searchName = this.lastWord
        this.getList()
      }
    }
  }
</script>

<style>

  .searchModule {
    margin-bottom: 20px;
  }
  .searchModule span {
    margin-right: 6px;
  }
  .searchModule > div {
    width: 200px;
    margin-right: 6px;
  }
  .searchModule_add {
    float: right;
  }
</style>



