<template>
  <div id="property">
    <Login v-if="login"></Login>
    <el-container v-else>
      <el-header>
        <top-bar :type="type"></top-bar>
      </el-header>
      <el-container>
        <el-main>
           <router-view/>
        </el-main>
      </el-container>
    </el-container>
    <!---->
  </div>
</template>

<script>
  import TopBar from '../../components/topBar.vue'
  import Login from '../../components/login.vue'

  export default {
    name: 'property',
    components: {
      TopBar, Login
    },
    data () {
      return {
        login: true,
        type: 'property'
      }
    },
    created: function () {
      if (!sessionStorage.getItem('userInfo')) {
        this.login = true
      } else {
        this.login = false
      }
    }
  }
</script>

<style scoped>
  .el-main {
    min-height: 84vh;
  }
  /*.el-main {*/
    /*color: #333;*/
    /*margin: 80px auto 20px;*/
    /*max-width: 1040px;*/
  /*}*/
</style>



