<template>
  <div id="opinionFeedback">
    <div class="searchModule">
      <span>时间</span>
      <el-date-picker
        v-model="startDate"
        type="date"
        size="small"
        placeholder="开始日期"
        value-format="yyyy-MM-dd">
      </el-date-picker> 至
      <el-date-picker
        v-model="endDate"
        type="date"
        size="small"
        placeholder="结束日期"
        value-format="yyyy-MM-dd">
      </el-date-picker>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label"
        :width="item.width"
      >
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Util from '../../../utils/util'
  export default {
    name: 'opinionFeedback',
    data () {
      return {
        startDate: '',
        endDate: '',
        pageNum: 1,
        total: 0,
        tableData: [],
        columns: [
          {prop: 'context', label: '反馈内容'},
          {prop: 'createDate', label: '反馈时间', width: '150px'},
          {prop: 'nickname', label: '用户昵称', width: '150px'},
          {prop: 'phone', label: '手机号', width: '150px'}
        ]
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search() {
        this.pageNum = 1
        this.lastEndDate = this.endDate
        this.lastStartDate = this.startDate
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.get('/feedback/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            startDate: this.startDate,
            endDate: this.endDate
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let feedback = res.resultData.feedback
              feedback.map((v, i)=>{
                v.createDate = Util.dateFormat(v.createDate)
                v.nickname = decodeURIComponent(v.nickname).replace(/\+/g, '')
                return v
              })
              _this.tableData = res.resultData.feedback
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.endDate = this.lastEndDate
        this.startDate = this.lastStartDate
        this.getList()
      }
    }
  }
</script>

<style>

</style>



