<template>
  <div class="advertiser">
    <div v-show="!isGetDetail">
      <header class="advertiser-header">
        <span>时间</span>
        <el-date-picker
          v-model="date"
          size="small"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
        <el-button type="primary" @click="search" class="advertiser-header-search" size="small">
          查询
        </el-button>
      </header>
      <section>
        <ul  class="advertiser-section-list">
          <li>
            <strong>活动数量</strong>
            <span> {{total}} </span>
          </li>
        </ul>
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
        >
          <el-table-column
            v-for="(item,key) in columns"
            :key="key"
            :prop="item.prop"
            :label="item.label"
          >
          </el-table-column>

          <el-table-column
            prop="handle"
            label="领取明细"
          >
            <template slot-scope="scope">
              <a @click="getDetail(scope)" tag="a"> 查看
              </a>
            </template>
          </el-table-column>
        </el-table>
      </section>
      <footer>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </footer>
    </div>

    <div v-show="isGetDetail">
      <detail :back="back"
              :total="tableData_detail"
              :activityId="currentActive"
              :tableData="tableData_detail"
      />
    </div>
  </div>
</template>

<script>

  import moment from 'moment';
  import Detail from './getDetail';
  import utils from '@/utils/util';

  export default{
    data(){
      return {
        date:[],
        tableData:[],
        tableData_detail:[],
        currentPage:1, //当前页数
        total:0,  //活动总数量
        total_detail: 0,
        sumPrestoreMoney:null,  //预存金额
        surplusMoney:null,  //剩余金额
        isSearch:false,  //带条件搜索
        isGetDetail: false, //显示详情
        currentActive: null, //

        columns:[
          {label:"活动名称",prop:"activityName"},
          {label:"投放时间",prop:"date"},
          {label:"参与人数",prop:"count"},
          {label:"领取人数",prop:"redMoney"},
          {label:"投放小区数",prop:"communityCount"},
        ]
      }
    },
    components:{
      Detail
    },
    mounted(){this.reqList()},
    watch:{},
    methods:{
      getDetail(item){
        if (item) {
          this.currentActive = item.row.id;
          this.isGetDetail = true;
          this.reqDetail()
        }
      },
      back(acive){
        this.isGetDetail = false;
      },
      reqList(pageNum,startDate,endDate){
        const enId = 39;
        if(!enId){ return; }
        this.$http.get("/activity/list/activityEffectList",{
          params:{
            pageNum:pageNum || 1,
            pageSize:10,
            startTime:startDate || "",
            endTime:endDate || "",
            enId,
          }
        }).then((res)=>{
          res = res.resultData;
          const data = res.activityModels || [];
          data.forEach((i)=>{
            i.date = `${i.startDate} 至 ${i.endDate}`
          });
          this.tableData = data;
          this.total = res.total;
          this.sumPrestoreMoney = res.sumPrestoreMoney;
          this.surplusMoney = res.surplusMoney;
        })
      },
      reqDetail(){
        const activityId = this.currentActive;
        if(activityId){
          this.$http.get("/activitystatistics/activityDetail",{
            params:{
              pageNum:1,
              pageSize:10,
              activityId,
            }
          }).then((res)=>{
            res = res.resultData;
            const data = res.accmList;
            data.forEach((i)=>{
              i.nickname = decodeURIComponent(i.nickname);
              i.createTime = moment(i.createTime).format("YYYY-MM-DD");
              i.phone = i.phone && `${i.phone.substr(0,3)}****${i.phone.substr(7)}`
            })
            this.tableData_detail = data;
            this.total_detail = res.total;
          })
        }
      },
      search(){

        let [startDate,endDate] = this.date || [];

        if(!(startDate && endDate)){
          this.isSearch = false;
          this.reqList(1)
        }else{
          this.isSearch = true;
          this.reqList(1,moment(startDate).format("YYYY-MM-DD"),moment(endDate).format("YYYY-MM-DD"))
        }
      },
      handleCurrentChange(pageNum) {
        if (this.isSearch) {  /* 携带搜索条件 */
          let [startDate,endDate] = this.date
          this.reqList(pageNum,startDate,endDate)
        } else {
          this.reqList(pageNum)
        }
      }
    }
  }

</script>

<style scoped>

  .advertiser > footer{
    text-align: right;
    margin-top:20px;
  }

  .advertiser-header{
    text-align: left;
  }

  .advertiser-header-search{
    margin-left: 15px;
  }
  footer{
    margin-top: 15px;
    text-align: right;
  }
</style>



