<template>
    <div id="enterpriseAccount">
        <div class="searchModule">
            <span>登录账号</span>
            <el-input size="small" v-model="account" placeholder="请输入登录账号"></el-input>
            <span>&nbsp;&nbsp;&nbsp;公司名</span>
            <el-input size="small" v-model="enName" placeholder="请输入公司名"></el-input>
            <el-button size="small" type="primary" @click="search">搜索</el-button>
            <el-button size="small" style="float: right;" type="primary" @click="handleEdit(1, 0)">新增</el-button>
        </div>
        <el-table
                :data="tableData"
                border
                header-cell-class-name="advertisement_table_header"
                cell-class-name="advertisement_table_cell"
                style="width: 100%">
            <el-table-column
                    prop="uid"
                    label="用户ID">
            </el-table-column>
            <el-table-column
                    prop="userName"
                    label="用户名">
            </el-table-column>
            <el-table-column
                    prop="account"
                    label="登录账号">
            </el-table-column>
            <el-table-column
                    prop="enName"
                    label="企业">
            </el-table-column>
            <el-table-column
                    prop="type"
                    label="企业类型">
                <template slot-scope="scope">
                    <span v-if="scope.row.type === '1'">广告公司</span>
                    <span v-else>物业公司</span>
                </template>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    prop="address"
                    label="操作"
                    width="100"
            >
                <template slot-scope="scope">
                    <a @click="handleEdit(scope.$index, scope.row.uid)">编辑</a>&nbsp;&nbsp;
                    <a @click="handleDelete(scope.$index, scope.row.uid)">删除</a>&nbsp;
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination" v-if="tableData.length">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="pageNum"
            :page-size="10"
            layout="prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>

        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
        >
            <span>确定要删除吗？</span>
            <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmDelete">确 定</el-button>
      </span>
        </el-dialog>
    </div>
</template>

<script>
  export default {
    name: 'enterpriseAccount',
    data () {
      return {
        enName: '',
        account: '',
        tableData: [],
        pageNum: 1,
        total: 0,
        dialogVisible: false,
        deleteUid: '',

        lastEnName: '',
        lastAccount: ''
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search() {
        this.pageNum = 1
        this.lastEnName = this.enName
        this.lastAccount = this.account
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.post('/backuser/list', {
          pageNum: this.pageNum,
          pageSize: 10,
          enName: this.enName,
          account: this.account,
          type: '1,2'
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.backuser
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleEdit (index, uid) {
        this.$router.push({
          path: '/addEnterpriseAccount',
          query: {
            uid: uid
          }
        })
      },
      handleDelete (index, uid) {
        this.dialogVisible = true
        this.deleteUid = uid
      },
      confirmDelete () {
        let _this = this
        this.$http.get('/backuser/delete', {
          params: {
            uid: this.deleteUid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message.success({
                message: '删除成功'
              })
              _this.pageNum = Math.ceil((_this.total - 1)/10)
              _this.dialogVisible = false
              _this.getList()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.enName = this.lastEnName
        this.account = this.lastAccount
        this.getList()
      }
    }
  }
</script>

<style scoped="">

</style>



