<template>
  <div id="callSetting">
    <div class="searchModule">
      <span>运营商</span>
      <el-select v-model="chargeOperator" size="small" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <span>&nbsp;&nbsp;&nbsp;&nbsp;状态</span>
      <el-select v-model="status" size="small"  placeholder="请选择">
        <el-option
          v-for="item in statusOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button size="small" type="primary" @click="getList">搜索</el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        key="chargeOperator"
        prop="chargeOperator"
        label="运营商">
      </el-table-column>
      <el-table-column
        key="chargeNumber"
        prop="chargeNumber"
        label="流量">
        <template slot-scope="scope">
          <span>{{scope.row.chargeNumber + scope.row.numberUnit}}</span>
        </template>
      </el-table-column>
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
      <el-table-column
        key="status"
        prop="status"
        label="状态">
        <template slot-scope="scope">
          <span v-if="scope.row.status === 0">启用</span>
          <span v-else>停用</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="address"
        label="操作"
        width="100"
      >
        <template slot-scope="scope">
          <a @click="handleEdit(scope.$index, scope.row)">编辑</a>&nbsp;
          <a @click="handleUpdate(scope.$index, scope.row, '1')" v-if="scope.row.status === 0">停用</a>
          <a @click="handleUpdate(scope.$index, scope.row, '0')" v-else>启用</a>&nbsp;
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <span v-if="updateStatus === '0'">确定要启用吗？</span>
      <span v-else>确定要停用吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmUpdate">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改"
      :visible.sync="dialogFormVisible"
      width="34%"
    >
      <el-form
        :model="form"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="100px"
        class="demo-ruleForm propertyForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="面值">
          <span>{{traffic}}</span>
        </el-form-item>
        <el-form-item label="面值">
          <span>{{faceValue}}</span>
        </el-form-item>
        <el-form-item label="成本价">
          <span>{{costPrice}}</span>
        </el-form-item>
        <el-form-item label="售卖价" prop="sale">
          <el-input v-model="form.sale" style="width: 200px" size="small"></el-input>
        </el-form-item>
        <br>
        <div class="operationBtn">
          <el-form-item>
            <el-button @click="dialogFormVisible = false">取消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </div>
      </el-form>

    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'callSetting',
    data () {
      var validateSale = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入售卖价'))
        } else if (!/^\d{1,3}(\.\d{1,2})?$/.test(value)) {
          return callback(new Error('输入不正确'))
        } else if (value == 0) {
          return callback(new Error('售卖价不能为0'))
        } else {
          callback()
        }
      }
      return {
        chargeOperator: '',
        status: '0',
        pageNum: 1,
        tableData: [],
        updateStatus: '0',
        faceValue: '',
        costPrice: '',
        traffic: '',
        form: {
          sale: '',
        },
        rules2: {
          sale: [
            { require: true, validator: validateSale, trigger: 'change' }
          ]
        },
        dialogVisible: false,
        dialogFormVisible: false,
        columns: [
          {prop: 'faceValue', label: '面值'},
          {prop: 'costPrice', label: '成本价格'},
          {prop: 'sellingPrice', label: '售卖价格'}
        ],
        options: [
          {label: '全部', value: ''},
          {label: '移动', value: '移动'},
          {label: '联通', value: '联通'},
          {label: '电信', value: '电信'}
        ],
        statusOptions: [
          {label: '停用', value: '1'},
          {label: '启用', value: '0'}
        ],
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      getList () {
        let _this = this
        this.$http.get('/charge/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 1000,
            chargeType: 1,
            chargeOperator: this.chargeOperator,
            status: this.status
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.charges
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleUpdate(index, row, type) {
        this.updateStatus = type
        this.dialogVisible = true
        this.chargeId = row.chargeId
      },
      handleEdit(index, row, type) {
        this.faceValue = row.faceValue
        this.costPrice = row.costPrice
        this.chargeId = row.chargeId
        this.form.sale = row.sellingPrice
        this.traffic = row.chargeNumber+row.numberUnit
        this.dialogFormVisible = true
      },
      confirmUpdate () {
        let _this = this
        this.$http.get('/charge/updateStatus', {
          params: {
            chargeId: this.chargeId,
            status: this.updateStatus,
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.dialogVisible = false
              _this.$message({
                message: '修改成功',
                type:' success'
              })
              _this.getList()
            }
          })
          .catch(function (err) {
          })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.confirmFormUpdate()
          } else {
            return false
          }
        })
      },
      confirmFormUpdate () {
        let _this = this
        this.$http.get('/charge/updateSellingPrice', {
          params: {
            chargeId: this.chargeId,
            sellingPrice: this.form.sale,
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.dialogFormVisible = false
              _this.$message({
                message: '修改成功',
                type:' success'
              })
              _this.$refs['ruleForm2'].resetFields();
              _this.getList()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style scoped>
  .operationBtn {
    button:nth-child(1) {
      margin-left: 35px;
    }
  }
</style>



