<template>
  <div id="propertyManagement">

    <div class="searchModule">
      <span>物业名称</span>
      <el-input size="small" v-model="searchName" placeholder="请输入物业名称"></el-input>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
      <el-button size="small" class="right"
                 @click="add" type="primary">新增物业</el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%"
    >
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
      <el-table-column
        prop="balance"
        label="操作"
        width="140px">
        <template slot-scope="scope">
          <a @click="handleEdit(scope.$index, scope.row)">编辑</a>&nbsp;
          <a @click="incomeDetails(scope.$index, scope.row)">收益明细</a>&nbsp;
        </template>
      </el-table-column>
    </el-table>

    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog :title="propertyId ? '编辑物业' : '添加物业'" key="dialogKey" :visible.sync="dialogFormVisible">
      <el-form
        :model="form"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="100px"
        class="demo-ruleForm propertyForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="物业名称" prop="propertyName" :label-width="formLabelWidth">
          <el-input v-model="form.propertyName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="contactPhone" :label-width="formLabelWidth">
          <el-input v-model="form.contactPhone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="联系人" prop="contactUser" :label-width="formLabelWidth">
          <el-input v-model="form.contactUser" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item
          v-for="(selects, index) in form.selects"
          label="管辖小区"
          prop="selects"
          key="index"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="selects.value"
            filterable
            remote
            reserve-keyword
            placeholder="请输入小区名称"
            :remote-method="remoteMethod"
            @focus="selectFocus"
            :loading="loading"
            ref="select"
            popper-class="selectDistrict"
            style="z-index: 2002"
          >
            <el-option
              v-for="(item, inx) in districtList"
              :key="item.value"
              :label="item.label2"
              :value="item.value">
              <span style="display: block;height: 14px">{{ item.label2 }}</span>
              <span style="color: #8492a6; font-size: 13px">{{ item.label1 }}</span>
            </el-option>
          </el-select>
          <el-button v-if="index === form.selects.length - 1" @click="addDistrict">新增</el-button>
          <el-button v-if="form.selects.length !== 1" @click="deleteDistrict(index)">删除</el-button>
        </el-form-item>
        <div class="operationBtn">
          <el-form-item>
            <el-button @click="cancel">取消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-dialog>

  </div>
</template>

<script>
  import { Message } from 'element-ui'

  export default {
    name: 'propertyManagement',
    data () {
      var validatePropertyName = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入物业名称'))
        } else if (value.length > 30){
          callback(new Error('最多输入10字符'))
        } else {
          callback()
        }
      }
      var validateContactPhone = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入联系方式'))
        } else if(!(/^1[3|5|6|7|8]\d{9}$/.test(value))) {
          callback(new Error('请输入正确联系方式'))
        } else {
          callback()
        }
      }
      var validateContactUser = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入联系人'))
        } else if (value.length > 10) {
          callback(new Error('最多输入10字符'))
        } else {
          callback()
        }
      }
      var validateSelects = (rule, value, callback) => {
        let flag = true
        value.map((v, i) => {
          if (v.value !== '') {
            flag = false
          }
        })
        if (flag) {
          return callback(new Error('请选择至少一个管辖小区'))
        } else {
          callback()
        }
      }
      return {
        searchName: '',
        tableData: [],
        pageNum: 1,
        loading: false,
        propertyId: '', // ''代表添加，否则代表编辑
        districtList: [],
        lastWord: '',
        dialogFormVisible: false,
        dialogKey: Date.now(),
        form: {
          propertyName: '',
          contactPhone: '',
          contactUser: '',
          cids: '',
          districtName: '',
          selects: [{
            value: '',
            key: Date.now(),
          }]
        },
        formLabelWidth: '120px',
        columns: [
          {prop: 'propertyId', label: '物业ID'},
          {prop: 'propertyName', label: '物业名称'},
          {prop: 'contactUser', label: '联系人'},
          {prop: 'contactPhone', label: '联系方式'},
          {prop: 'districtList', label: '管辖小区'},
          {prop: 'brokerageMoney', label: '佣金分红（元）'},
          {prop: 'withdrawMoney', label: '提现金额（元）'},
          {prop: 'residueMoney', label: '剩余金额（元）'}
        ],
        rules2: {
          propertyName: [
            { required: true, validator: validatePropertyName, trigger: 'change' }
          ],
          contactPhone: [
            { required: true, validator: validateContactPhone, trigger: 'change' }
          ],
          contactUser: [
            { required: true, validator: validateContactUser, trigger: 'change' }
          ],
          selects: [
            { required: true, validator: validateSelects, trigger: 'change' }
          ]
        }
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search () {
        this.pageNum = 1
        this.lastWord = this.searchName
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.get('/property/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            propertyName: this.searchName
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              res.resultData.property.map((v,i) => {
                v.districtList = ''
                v.communities.map((c, j) => {
                  v.districtList += c.cName + (j === v.communities.length - 1 ? '' : '、')
                })
                return v
              })
              _this.tableData = res.resultData.property
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      remoteMethod (query) {
        if (query !== '') {
          this.loading = true
          setTimeout(() => {
            this.loading = false
            this.districtList = []
            this.getDistrictList(query)
          }, 200);
        } else {
          this.districtList = []
        }
      },
      selectFocus(a) {
        this.districtList = []
      },
      getDistrictList (query) {
        let _this = this
        this.$http('/community/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            search: query
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let community = res.resultData.community
              community.map((v, i) => {
                _this.districtList.push({
                  label1: v.province + v.city + v.district,
                  label2: v.cName,
                  value: v.cid
                })
              })
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      addDistrict () {
        this.form.selects.push({
          value: '',
          key: Date.now(),
        })
      },
      deleteDistrict (index) {
        this.form.selects.splice(index, 1)
        return this.form.selects
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.saveHttp()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      saveHttp () {
        let _this = this, cids = []
        if(this.unique(this.form.selects).length < this.form.selects.length) {
          Message.error({
            message: '管辖小区不能重复'
          })
          return
        }
        this.form.selects.map((v, i) => {
          if (this.propertyId !== '') {
            if (v.value !== '' && v.value != v.key) { // 如果value == key证明没修改过，可以直接key,否则取value
              cids.push(v.value)
            } else {
              cids.push(v.cid)
            }
          } else {
            v.value !== '' && cids.push(v.value)
          }
        })
        this.$http.post('/property/save', {
          propertyName: this.form.propertyName,
          contactUser: this.form.contactUser,
          contactPhone: this.form.contactPhone,
          cids: cids.join(','),
          propertyId: this.propertyId
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: _this.propertyId != '' ? '修改成功' : '添加成功',
                type: 'success'
              })
              _this.cancel()
              _this.getList()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      cancel() {
        this.form = {
          selects: [{
            value: '',
            key: Date.now(),
          }]
        }
        this.propertyId = ''
        this.$refs['ruleForm2'].resetFields();
        this.dialogFormVisible = false
      },
      unique(array) {
        var obj = {};
        return array.filter(function(item, index, array){
          return obj.hasOwnProperty(typeof item.value + JSON.stringify(item.value)) ? false : (obj[typeof item.value + JSON.stringify(item.value)] = true)
        })
      },
      add() {
        this.form = {
          selects: [{
            value: '',
            key: Date.now(),
          }]
        }
        this.propertyId = ''
        this.dialogFormVisible = true
      },
      handleEdit (index, row) {
        let _this = this
        this.$http.get('/property/get', {
          params: {
            propertyId: row.propertyId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let property = res.resultData.property, selects = []
              property.communities.map((v,i)=>{
                selects.push({
                  value: v.cName,
                  key: v.cName,
                  cid: v.cid
                })
              })
              _this.form = {
                propertyName: property.propertyName,
                contactPhone: property.contactPhone,
                contactUser: property.contactUser,
                selects
              }
              _this.propertyId = property.propertyId
              _this.dialogFormVisible = true
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      incomeDetails (index, row) {
        this.$router.push({
          path: '/incomeDetails',
          query: { propertyId: row.propertyId, name: row.propertyName, enCode: row.enCode }
        })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.searchName = this.lastWord
        this.getList()
      }
    }
  }
</script>

<style>
  .operationBtn {
    text-align: right;
  }
  .selectDistrict .el-select-dropdown__item {
    height: 54px;
    line-height:54px;
  }
</style>



