<template>
  <div id="incomeDetails">
    <h2>{{ title }}</h2>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'incomeDetails',
    data () {
      return {
        propertyId: this.$route.query.propertyId,
        title: this.$route.query.name,
        pageNum: 1,
        tableData: [],
        total: 0,
        columns: [
          {prop: 'activityId', label: '活动ID'},
          {prop: 'activityName', label: '活动名称'},
          {prop: 'cName', label: '投放小区'},
          {prop: 'count', label: '领取人数'},
          {prop: 'propertyMoney', label: '佣金分红（元）'}
        ]
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      getList () {
        let _this = this
        this.$http.post('/activitystatistics/property', {
          pageNum: this.pageNum,
          pageSize: 10,
          propertyId: this.propertyId,
          enCode: this.$route.query.enCode
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.padms
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.getList()
      }
    }
  }
</script>

<style>

</style>



