<template>
    <div id="userManagement">
        <div class="searchModule">
            <span>昵称</span>
            <el-input size="small" v-model="nickname" placeholder="请输入昵称"></el-input>
            <span>&nbsp;&nbsp;&nbsp;手机号</span>
            <el-input size="small" v-model="phone" placeholder="请输入手机号"></el-input>
            <el-button size="small" type="primary" @click="search">搜索</el-button>
        </div>
        <el-table
          :data="tableData"
          border
          header-cell-class-name="advertisement_table_header"
          cell-class-name="advertisement_table_cell"
          style="width: 100%">
          <el-table-column
            v-for="(item,key) in columns"
            :key="key"
            :prop="item.prop"
            :label="item.label">
          </el-table-column>
        </el-table>
        <div class="pagination" v-if="tableData.length">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="pageNum"
            :page-size="10"
            layout="prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
    </div>
</template>

<script>
    import Util from '../../../utils/util'
    export default {
      name: 'userManagement',
      data () {
        return {
          nickname: '',
          phone: '',
          lastNickname: '',
          lastPhone: '',
          tableData: [],
          columns: [
            {prop: 'clientUserId',label: '用户ID', width: 100},
            {prop: 'nickname',label: '昵称'},
            {prop: 'sex',label: '性别'},
            {prop: 'phone',label: '手机号'},
            {prop: 'province',label: '地区'},
            {prop: 'bank',label: '开户银行'},
            {prop: 'bankAccount',label: '银行卡号'},
            {prop: 'bankAccountName',label: '户名'},
            {prop: 'balance',label: '余额（元）'},
            {prop: 'createTime',label: '注册时间'}
          ],
          pageNum: 1,
          total: 0
        }
      },
      mounted () {
        this.getList()
      },
      methods: {
        search() {
          this.lastNickname = this.nickname
          this.lastPhone = this.phone
          this.pageNum = 1
          this.getList()
        },
        getList () {
          let _this = this
          this.$http.post('/client/list', {
            pageNum: this.pageNum,
            pageSize: 10,
            nickname: this.nickname,
            phone: this.phone
          })
            .then(function (res) {
              if (res.errorCode === '0') {
                let clientuser = res.resultData.clientuser
                clientuser && clientuser.map((v, i)=>{
                  v.createTime = Util.dateFormat(v.createTime)
                  v.nickname = decodeURIComponent(v.nickname).replace(/\+/g, '')
                  return v
                })
                _this.tableData = clientuser
                _this.total = res.resultData.total
              }
            })
            .catch(function (err) {
              console.log(err)
            })
        },
        handleCurrentChange (val) {
          this.pageNum = val
          this.nickname = this.lastNickname
          this.phone = this.lastPhone
          this.getList()
        }
      }
    }
</script>

<style scoped="">

</style>



