<template>
  <div id="platformPumping">
    <div class="searchModule">
      <span> 活动ID </span>
      <el-input size="small" v-model="searchId" style="width:100px" placeholder="请输入活动id"></el-input>
      <span>&nbsp;&nbsp;&nbsp;&nbsp;投放小区</span>
      <el-input size="small" v-model="cName" placeholder="请输入内容"></el-input>
      <span>&nbsp;&nbsp;&nbsp;&nbsp;广告主</span>
      <el-input size="small" v-model="enName" placeholder="请输入内容"></el-input>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
    </div>
    <p>平台抽佣总计：{{platformMoneyTotal}}元</p>
    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'platformPumping',
    data () {
      return {
        cName: '',
        enName: '',
        lastCName: '',
        lastEnName: '',
        searchId: "",
        lastSearchId: "",

        pageNum: 1,
        total: 0,
        tableData: [],
        platformMoneyTotal: '',
        columns: [
          {prop: 'activityName', label: '活动'},
          {prop: 'enName', label: '广告主'},
          {prop: 'cName', label: '投放小区'},
          {prop: 'startDate', label: '投放时间'},
          {prop: 'count', label: '领取人数'},
          {prop: 'redMoney', label: '单个红包金额（元）'},
          {prop: 'platformMoney', label: '系统抽佣（元）'}
        ],
        options: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '待处理'
        }, {
          value: '1',
          label: '处理完成'
        }],
        status: ''
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search() {
        this.pageNum = 1
        this.lastCName = this.cName
        this.lastEnName = this.enName
        this.lastSearchId = this.searchId
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.get('/activitystatistics/platform/statistics', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            cName: this.cName,
            enName: this.enName,
            activitId: this.searchId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              console.log(res);
              _this.tableData = res.resultData.models
              _this.total = res.resultData.total
              _this.platformMoneyTotal = res.resultData.platformMoneyTotal
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.cName = this.lastCName
        this.enName = this.lastEnName
        this.searchId = this.lastSearchId
        this.getList()
      }
    }
  }
</script>

<style>

</style>



