<template>
  <div id="addAdvertiser" class="addAvertiser">
    <h3 v-if="enId === ''">添加广告主</h3>
    <h3 v-else>编辑广告主</h3>
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
      label-position="center"
      size="small"
    >
      <el-form-item label="广告主" prop="enName">
        <el-input v-model="ruleForm.enName" placeholder="请输入广告主" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="广告负责人" prop="contactUser">
        <el-input v-model="ruleForm.contactUser" placeholder="请输入广告负责人" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="联系方式" prop="contactPhone">
        <el-input v-model="ruleForm.contactPhone" placeholder="请输入联系方式" auto-complete="off"></el-input>
      </el-form-item>
      <br>
      <el-form-item>
        <el-button @click="back">返回</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'addAdvertiser',
    data () {
      var checkPhone = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入手机号'))
        } else if (!(/^1[34578]\d{9}$/.test(value))) {
          callback(new Error('请输入正确手机号'))
        } else {
          callback()
        }
      }
      return {
        enId: this.$route.query.enId || '',
        ruleForm: {
          enName: '',
          contactUser: '',
          contactPhone: ''
        },
        rules: {
          enName: [
            {required: true, message: '请输入广告主', trigger: 'change'},
            {min: 1, max: 30, message: '最多输入30字符', trigger: 'change'}
          ],
          contactUser: [
            {required: true, message: '请输入负责人', trigger: 'change'},
            {min: 1, max: 10, message: '最多输入10字符', trigger: 'change'}
          ],
          contactPhone: [
            { required: true, validator: checkPhone, trigger: 'change' }
          ]
        }
      }
    },
    mounted () {
      this.enId !== '' && this.get()
    },
    methods: {
      get() {
        let _this = this
        this.$http.get('/enterprise/get', {
          params: {
            enId: this.enId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let enterprise = res.resultData.enterprise
              _this.ruleForm = {
                enName: enterprise.enName,
                contactUser: enterprise.contactUser,
                contactPhone: enterprise.contactPhone
              }
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      back () {
        this.$router.push('/advertiserManagement')
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.addAdvertiserHttp()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      addAdvertiserHttp () {
        let _this = this
        this.$http.get('/enterprise/save', {
          params: {
            enName: this.ruleForm.enName,
            contactUser: this.ruleForm.contactUser,
            contactPhone: this.ruleForm.contactPhone,
            enId: this.enId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '保存成功',
                type: 'success'
              })
              _this.$router.push('/advertiserManagement')
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style scoped>
  .addAvertiser .el-input--small {
    width: 250px;
  }
</style>



