<template>
  <div class="advertiser-result-detail">
    <header>
      <el-button @click="skip" size="small">
        返回
      </el-button>
      <h3> {{name}} </h3>
      <span> 投放时间：{{ date }} </span>
    </header>
    <section>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
      >
        <el-table-column
          v-for="(item,key) in columns"
          :key="key"
          :prop="item.prop"
          :label="item.label"
        >
        </el-table-column>
      </el-table>
    </section>
  </div>
</template>

<script>

  import moment from 'moment';

  export default{
    props: ['back', 'tableData', 'total'],
    data(){
      return {
        count:"",
        name:"",
        date:"",
        prestoreMoney:"",
        surplusMoney:"",

        columns: [
          {label: "用户昵称", prop: "nickname"},
          {label: "手机号", prop: "phone"},
          {label: "领取时间", prop: "createTime"},
        ],
      }
    },
    methods: {
      skip(){
        this.$props.back();
      }
    },
    mounted(){

    },
  }

</script>

<style scoped>
  .advertiser-result-detail {
    text-align: left;
    font-size: 14px;
  }

  .advertiser-section-list {

  }
</style>



