/**
 * http配置
 */
// 引入axios以及element ui中的loading和message组件
import axios from 'axios'
import { Message } from 'element-ui'

// 超时时间
axios.defaults.timeout = 5000

// 基础路径
axios.defaults.baseURL = "http://120.78.12.39:80";


/**
 *
 * req 中间件
 */
axios.interceptors.request.use(config => {
  let userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
  // element ui Loading方法
  // loadinginstace = Loading.service({ fullscreen: true })
  if (config.url !== '/backuser/login') { // 除了登录其他都需要加用户信息
    if (config.method === 'get') {
      config.params.user_id = 56
      config.params.user_token = 'c97c49d8e0cf487d98a6ea9e7e3b559b'
    } else if(config.method === 'post') {
      const formData = new FormData()
      let data = config.data
      data.user_id = 56
      data.user_token = 'c97c49d8e0cf487d98a6ea9e7e3b559b'
      for (let key in data) { formData.append(key, data[key]) }
      config.data = formData
    }
  }
  return config
}, error => {
  // loadinginstace.close()
  Message.error({
    message: '加载超时'
  })
  return Promise.reject(error)
})

/**
 *
 *  http响应拦截器
 *  response 中间件
 */
axios.interceptors.response.use(data => {

  let returnData = JSON.parse(decodeURIComponent(data.data))
  if (returnData.errorCode === '0') {

  } else {
      Message.error({
        message: returnData.errorMsg
      })
  }
  return returnData
}, error => {
  Message.error({
    message: '加载失败'
  })
  return Promise.reject(error)
})

export default axios



