<template>
  <div id="TopBar">
    <span class="name" v-if="!_props.type"> <img src="../assets/logo.png" alt=""> 广告管理平台 </span>
    <span class="name" v-else> <img src="../assets/logo.png" alt=""> {{ enterpriseName }} </span>
    <span class="headerMenu" v-if="_props.type === 'property'">
      <a :class="activeInx === 0 ? 'active' : ''" @click="linkUrl('/', 0)">广告收益</a>
      <a :class="activeInx === 1 ? 'active' : ''" @click="linkUrl('/outMoneyHistory', 1)">提现记录</a>
      <a :class="activeInx === 2 ? 'active' : ''" @click="linkUrl('/partAdvertiser', 2)">管辖小区</a>
    </span>
    <span class="headerMenu" v-if="_props.type === 'advertiser'">
      <a :class="activeInx === 0 ? 'active' : ''" @click="linkUrl('/', 0)">广告效果</a>
    </span>
    <div class="headerImg"><span>{{ headerName }}</span>
      <el-menu class="el-menu-demo" mode="horizontal" @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
        <el-submenu index="2">
          <template slot="title">{{ userName }}</template>
          <el-menu-item index="update">修改密码</el-menu-item>
          <el-menu-item index="quit">退出</el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <el-dialog title="修改密码" append-to-body :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm2"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="200px"
        class="demo-ruleForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="旧密码" prop="oldPass">
          <el-input style="width: 250px" type="password" v-model="ruleForm2.oldPass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="pass">
          <el-input style="width: 250px" type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" prop="newPass">
          <el-input style="width: 250px" type="password" v-model="ruleForm2.newPass" auto-complete="off"></el-input>
        </el-form-item>
        <br>
        <el-form-item>
          <el-button class="submitBtn" type="primary" @click="submitForm('ruleForm2')">确定</el-button>
          <el-button class="submitBtn" @click="dialogFormVisible=false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'TopBar',
    props: ['type'],
    data () {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'))
        } else {
          if (this.ruleForm2.oldPass === value) {
            callback(new Error('新旧密码不能一致'))
          }
          callback()
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          callback()
        }
      }
      return {
        enterpriseName: sessionStorage.getItem('enterpriseName'),
        userName: JSON.parse(sessionStorage.getItem('userInfo')).userName || '',
        headerName: '',
        dialogFormVisible: false,
        activeInx: 0,
        ruleForm2: {
          pass: '',
          oldPass: '',
          newPass: ''
        },
        rules2: {
          oldPass: [
            {required: true, message: '请输入新密码', trigger: 'blur'},
            {min: 6, max: 20, message: '请输入6-20位密码', trigger: 'blur'}
          ],
          pass: [
            { required: true, validator: validatePass, trigger: 'blur' },
            {min: 6, max: 20, message: '请输入6-20位密码', trigger: 'blur'}
          ],
          newPass: [
            { required: true, validator: validatePass2, trigger: 'blur' },
            {min: 6, max: 20, message: '请输入6-20位密码', trigger: 'blur'}
          ]
        },
        formLabelWidth: '120px'
      }
    },
    mounted () {
      if(this.$router.history.current.path == '/outMoneyHistory') {
        this.activeInx = 1
      } else if(this.$router.history.current.path == '/partAdvertiser') {
        this.activeInx = 2
      }
      this.headerName = this.userName.substr(this.userName.length - 1, this.userName.length) || ''
    },
    methods: {
      handleSelect (value) {
        if (value === 'update') {
          this.dialogFormVisible = true
        } else {
          this.quit()
        }
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.updatePass()
          } else {
            return false
          }
        })
      },
      updatePass () {
        let _this = this
        this.$http.get('/backuser/updatePassword', {
          params: {
            newPassword: this.ruleForm2.newPass,
            oldPassword: this.ruleForm2.oldPass,
            uid: JSON.parse(sessionStorage.getItem('userInfo')).uid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '修改成功，正在为您退出系统',
                type: 'success'
              })

              setTimeout(function () {
                _this.quit()
              }, 1000)
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      quit () {
        this.$emit('updateLoginStatus', true) //父子组件之间的通信
        sessionStorage.clear()
        this.$router.push('/login')
        window.location.reload()
      },
      linkUrl (url, inx) {
        this.activeInx = inx
        this.$router.push(url)
        return this.activeInx
      }
    }
  }
</script>

<style>
  #TopBar {
    text-align: left;
    padding-right: 20px;
  }
  .name {
    padding-left: 20px;
    text-align: left;
  }
  .name img {
    float: left;
    margin-left: 20px;
    width: 49px;
    height: 49px;
    border-radius: 50%;
    margin-top: 4px;
  }
  .headerImg {
    float: right;
  }
  .headerImg span {
    width: 36px;
    height: 36px;
    margin-top: 12px;
    background: #ffd04b;
    border-radius: 22px;
    color: #fff;
    display: inline-block;
    float: left;
    line-height: 36px;
    margin-right: 10px;
    text-align: center;
  }
  .headerImg .el-menu-demo {
    display: inline-block;
    background-color: rgb(36, 47, 65) !important;
  }
  .headerImg .el-menu-demo li > div {
    background-color: rgb(36, 47, 65) !important;
  }
  .submitBtn {
    margin-left: 10px;
  }
  .headerMenu a {
    color: #fff;
    margin-left: 40px;
    cursor: pointer;
    padding: 5px;
    display: inline-block;
    height: 60px;
    box-sizing: border-box;
  }
  .headerMenu a.active {
    border-bottom: 3px solid #ffd04b;
  }
</style>



