<template>
  <div id="userPresentAudit">
    <div class="searchModule">
      <span>用户昵称</span>
      <el-input size="small" v-model="nickname" placeholder="请输入内容"></el-input>
      <span>&nbsp;&nbsp;&nbsp;&nbsp;手机号</span>
      <el-input size="small" v-model="phone" placeholder="请输入内容"></el-input>
      <span>&nbsp;&nbsp;&nbsp;状态</span>
      <el-select v-model="status" size="small" placeholder="全部">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
    </div>

    <div class="search-middle">
      <el-button size="small" type="primary" @click="openWithDraw(true)"> 设置提现额度 </el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
      <el-table-column
        key="8"
        label="状态">
        <template slot-scope="scope">
          <span>{{scope.row.status === 0 ? '待处理' : '处理完成'}}</span>
        </template>
      </el-table-column>
      <el-table-column
        key="9"
        label="操作"
        width="80px">
        <template slot-scope="scope">
          <a  v-if="scope.row.serialNumber == ''" @click="operation(scope.$index, scope.row)">处理</a>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="40%"
      @close="resetForm('ruleForm2')"
    >
      <el-form
        :model="form"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="130px"
        class="demo-ruleForm propertyForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="用户昵称：">
          <span>{{form.nickname}}</span>
        </el-form-item>
        <el-form-item label="手机号：">
          <span>{{form.phone}}</span>
        </el-form-item>
        <el-form-item label="开户银行：">
          <span>{{form.bank}}</span>
        </el-form-item>
        <el-form-item label="银行卡号：">
          <span>{{form.bankAccount}}</span>
        </el-form-item>
        <el-form-item label="户主：">
          <span>{{form.accountUser}}</span>
        </el-form-item>
        <el-form-item label="提现金额：">
          <span>{{form.withdrawMoney}}</span>
        </el-form-item>
        <el-form-item label="交易流水号：" prop="serialNumber">
          <el-input v-model="form.serialNumber" style="width: 300px"
                    size="small" placeholder="请填写银行打款的流水号">
          </el-input>
        </el-form-item>

        <div>
          <el-form-item>
            <el-button style="marginLeft: 50px" @click="resetForm('ruleForm2')">取消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-dialog>


    <el-dialog
      title="设置提现额度"
      :visible.sync="withDrawVisible"
      width="30%"
    >
      <el-form>
        <el-form-item
          label="提现额度限制"
        >
          <el-input-number
            class="withDrawInput"
            v-model="withDrawValue"
            size="small"
          >
          </el-input-number>
          元
        </el-form-item>

        <el-form-item>
          <el-button style="float:right" type="primary" @click="withDrawSubmit"> 确定 </el-button>
        </el-form-item>

      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'userPresentAudit',
    data () {
      var validateSerialNumber = (rule, value, callback) => {
        if (!value || value.trim() == '' || value.length > 50) {
          return callback(new Error('请输入正确交易流水号'))
        } else {
          callback()
        }
      }
      return {
        nickname: '',
        phone: '',
        pageNum: 1,
        withDrawVisible: false,
        withDrawValue:"", //提现金额
        tableData: [],
        dialogVisible: false,
        columns: [
          {prop: 'serialNumber', label: '流水号'},
          {prop: 'nickname', label: '用户昵称'},
          {prop: 'phone', label: '手机号'},
          {prop: 'withdrawMoney', label: '提现金额（元）'},
          {prop: 'accountUser', label: '户主'},
          {prop: 'bank', label: '开户银行'},
          {prop: 'bankAccount', label: '银行卡号'},
        ],
        options: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '待处理'
        }, {
          value: '1',
          label: '处理完成'
        }],
        status: '',
        form: {serialNumber: ''},
        rules2: {
          serialNumber: [
            { required: true, validator: validateSerialNumber, trigger: 'change' }
          ]
        },
        lastPhone: '',
        lastNickname: '',
        lastStatus: '',
        operationIndex: '',
      }
    },
    mounted () {
      this.getList();
    },
    methods: {
      search() {
        this.pageNum = 1
        this.lastPhone = this.phone
        this.lastNickname = this.nickname
        this.lastStatus = this.status
        this.getList()
      },
      openWithDraw(type) {
        if (type) {
          this.$http.get("/finance/getWithdrawalAmount",{
            params:{},
          }).then(res => {
            res = res.resultData.withdrawalAmount;
            this.withDrawValue = res;
          })
          this.withDrawVisible = true;
        } else {
          this.withDrawVisible = "";
          this.withDrawVisible = false;
        }
      },
      withDrawSubmit() {
         const amount = this.withDrawValue;
         if (/^\d+\.?\d{1,2}$/.test(amount)) {
           this.$http.post("/finance/setWithdrawalAmount",{
             withdrawalAmount: amount,
           }).then(res => {
             if (res.errorCode === '0') {
               this.$message('设置成功')
               this.openWithDraw(false);
             }
           })
         } else {
           this.$message("请输入1-3有效正整数");
         }
      },
      getList() {
        let _this = this
        this.$http.get('/finance/withdraw/clientuser/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            phone: this.phone,
            nickname: this.nickname,
            status: this.status
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.clientUserWithdrawModels
              _this.tableData.map((v,i)=>{
                v.nickname = decodeURIComponent(v.nickname).replace(/\+/g, '')
                return v
              })
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      operation (index, row) {
        this.dialogVisible = true
        this.operationIndex = index;
        for (var key in row) {
          this.form[key] = row[key]
        }
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.confirmFormUpdate()
          } else {
            return false
          }
        })
      },
      confirmFormUpdate() {
        let _this = this
        this.$http.post('/finance/withdraw/clientuser/handle',{
          cuwId: this.form.cuwId,
          serialNumber: this.form.serialNumber.trim()
        })
          .then(function(res) {
            if (res.errorCode === '0') {
              if (_this.operationIndex == (_this.total - 1)%10) {
                _this.pageNum = Math.ceil((_this.total - 1)/10)
              }
              _this.resetForm('ruleForm2')
              _this.getList()
            }
          })
      },
      resetForm() {
        this.form = {serialNumber: ''}
        this.$refs['ruleForm2'].resetFields();
        this.dialogVisible = false
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.phone = this.lastPhone
        this.nickname = this.lastNickname
        this.status = this.lastStatus
        this.getList()
      }
    }
  }
</script>

<style scoped>
 .search-middle{
   text-align: right;
   margin-bottom: 10px;
 }
 .withDrawInput{
   display: inline-block;
   width: 150px;
 }
</style>



