<template>
  <div>
    <span> 共 {{total}} 条 </span>
    <el-table
      ref="multipleTable"
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label"
      >
      </el-table-column>
    </el-table>

    <footer>
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </footer>
  </div>
</template>

<script>

  import util from '@/utils/util';

  export default{
    data(){
      return {
        tableData:[],
        total:0,
        columns:[
          {label:"小区ID",prop:"cid"},
          {label:"小区名称",prop:"cName"},
          {label:"地址",prop:"location"},
          {label:"广告负责人",prop:"principal"},
          {label:"广告位数量",prop:"posterNumber"},
          {label:"小区详情",prop:"cDesc"},
          {label:"小区规模",prop:"cScale"},
        ],
      }
    },
    mounted(){
      this.reqList();
    },

    methods: {
      reqList(pageNum = 1, pageSize = 10) {
        this.$http.get('/property/list/community', {
          params: {
            pageNum,
            pageSize,
            enCode: util.getSession('enCode'),
            cName: '',
          }
        }).then(res => {

          res = res.resultData;

          const data = res.communities || [];

          data.forEach((i) => {
            i.location = i.province + i.city + i.district + i.address;
          })

          this.total = res.total;

          this.tableData = res.communities || [];

        })
      },
      handleCurrentChange(page){
        this.reqList(page);
      },
    }
  }

</script>

<style scoped>
  footer{
    text-align: right;
    margin-top: 20px;
  }
</style>



