<template>
  <div id="permissionManagement">
    <div class="searchModule">
      <span>登录账号</span>
      <el-input size="small" v-model="searchName" placeholder="请输入内容"></el-input>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
      <el-button
        size="small"
        class="searchModule_add"
        icon="el-icon-plus"
        type="primary"
        @click="handleEdit(1, 0)"
      >
        新增人员
      </el-button>
    </div>
    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        prop="uid"
        label="人员ID"
        width="80px"
      >
      </el-table-column>
      <el-table-column
        prop="userName"
        label="人员名称"
        width="140px"
      >
      </el-table-column>
      <el-table-column
        prop="account"
        label="登录账号"
        width="140px"
      >
      </el-table-column>
      <el-table-column
        prop="pidDESC"
        label="权限"
      >
      </el-table-column>
      <el-table-column
        fixed="right"
        prop="address"
        label="操作"
        width="100"
      >
        <template slot-scope="scope">
          <a @click="handleEdit(scope.$index, scope.row.uid)">编辑</a>&nbsp;
          <a @click="handleDelete(scope.$index, scope.row.uid)">删除</a>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <span>确定要删除吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmDelete">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'permissionManagement',
    data () {
      return {
        dialogVisible: false,
        searchName: '',
        pageNum: 1,
        total: 0,
        tableData: [],
        deleteUid: null,

        lastSearchName: ''
      }
    },
    mounted () {
      this.getPermissionList()
    },
    methods: {
      search () {
        this.pageNum = 1
        this.lastSearchName = this.searchName
        this.getPermissionList()
      },
      getPermissionList () {
        let _this = this
        this.$http.get('/backuser/list', {
          params: {
            type: 0,
            pageNum: this.pageNum,
            pageSize: 10,
            account: this.searchName || ''
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.tableData = res.resultData.backuser
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
          })
      },
      handleEdit (index, uid) {
        this.$router.push({
          path: '/addpersonnel',
          query: {
            uid: uid
          }
        })
      },
      handleDelete (index, uid) {
        this.dialogVisible = true
        this.deleteUid = uid
        return this
      },
      confirmDelete () {
        let _this = this
        this.$http.get('/backuser/delete', {
          params: {
            uid: this.deleteUid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message.success({
                message: '删除成功'
              })
              _this.dialogVisible = false
              _this.getPermissionList()
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.searchName = this.lastSearchName
        this.getPermissionList()
      }
    }
  }
</script>

<style>
  .searchModule {
    margin-bottom: 20px;
  }
  .searchModule span {
    margin-right: 6px;
  }
  .searchModule > div {
    width: 200px;
    margin-right: 6px;
  }
  .searchModule_add {
    float: right;
  }
</style>



