<template>
    <div id="addEnterpriseAccount" class="addEnterpriseAccount">
        <h3 v-if="uid === 0">添加企业账号</h3>
        <h3 v-else>编辑企业账号</h3>
        <el-form
                :model="ruleForm2"
                status-icon
                :rules="rules2"
                ref="ruleForm2"
                label-width="100px"
                class="demo-ruleForm"
                label-position="center"
                size="small"
        >
            <el-form-item label="用户名" prop="userName">
                <el-input v-model="ruleForm2.userName" placeholder="1-10字"></el-input>
            </el-form-item>
            <el-form-item label="登录账号" prop="account">
                <el-input v-model.number="ruleForm2.account" placeholder="请填写手机号"></el-input>
            </el-form-item>
            <el-form-item label="登录密码" prop="password">
                <el-input v-bind:disabled="disabled" type="password" placeholder="6-20位数字或字母" v-model="ruleForm2.password" auto-complete="off"></el-input>
                <el-button size="small" v-if="disabled === true" @click="resetPass">重置密码</el-button>
                <span style="color: red">{{ resetHint }}</span>
            </el-form-item>
            <el-form-item label="企业类型" prop="enterpriseType">
                <el-select size="small" placeholder="全部" v-model="ruleForm2.enterpriseType" @change="changeSelect">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="企业名称" prop="enterpriseName">
                <el-select
                        v-model="ruleForm2.enterpriseName"
                        filterable
                        remote
                        reserve-keyword
                        placeholder="请输入要关联企业名称"
                        :remote-method="remoteMethod"
                        :loading="loading">
                    <el-option
                            v-for="item in optionsName"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <div class="operationBtn">
                <el-form-item>
                    <el-button @click="back">返回</el-button>
                    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>

<script>
  export default {
    name: 'addEnterpriseAccount',
    data () {
      var validateUserName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入正确用户名'))
        } else if ( value.length > 10) {
          return callback(new Error('最多输入10字符'))
        } else {
          callback()
        }
      }
      var validateAccount = (rule, value, callback) => {
        if (!value || (!/^1[3,4,5,6,7,8]\d{9}$/.test(value))) {
          callback(new Error('请输入正确手机号'))
        } else {
          callback()
        }
      }
      var validatePassword = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入登录密码'))
        } else if (value.length < 6 || value.length > 20) {
          callback(new Error('输入6-20登录密码'))
        } else {
          callback()
        }
      }
      var validateEnterpriseName = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入企业名称'))
        } else {
          callback()
        }
      }

      return {
        checkList: [],
        uid: this.$route.query.uid || 0,
        disabled: false,
        resetHint: '',
        options: [{
          value: '2',
          label: '物业公司'
        }, {
          value: '1',
          label: '广告公司'
        }],
        optionsName: [],

        loading: false,
        ruleForm2: {
          userName: '',
          account: '',
          password: '',
          enterpriseType: '2',
          enterpriseName: '',
          oldEnterpriseName: '',
          oldEnCode: ''
        },
        rules2: {
          userName: [
            { required: true, validator: validateUserName, trigger: 'change' }
          ],
          account: [
            { required: true, validator: validateAccount, trigger: 'change' }
          ],
          password: [
            { required: true, validator: validatePassword, trigger: 'change' }
          ],
          enterpriseName: [
            { required: true, validator: validateEnterpriseName, trigger: 'change' }
          ],
        }
      }
    },
    created () {
      let _this = this
      this.$http('/backuser/getPids', {
        params: {}
      })
        .then(function (res) {
          if (res.errorCode === '0') {
            let pids = res.resultData ? res.resultData.pids : []
            _this.checkList = pids
          }
        })
        .catch(function (err) {
          console.log(err)
        })
      if (this.uid !== 0) {
        this.get()
        this.disabled = true
        return this.disabled
      }
    },
    methods: {
      get () {
        let _this = this
        this.$http('/backuser/get', {
          params: {
            uid: this.uid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let backuser = res.resultData.backuser
              _this.ruleForm2.userName = backuser.userName
              _this.ruleForm2.account = backuser.account
              _this.ruleForm2.password = '1234567890'
              _this.ruleForm2.enterpriseType = backuser.type
              _this.ruleForm2.enterpriseName = backuser.enName
              _this.ruleForm2.oldEnterpriseName = backuser.enName
              _this.ruleForm2.oldEnCode = backuser.enCode
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      checkboxChange (value) {
        this.defaultCheckList = value
        return this.defaultCheckList
      },
      changeSelect() {
        this.ruleForm2.enterpriseName = ''
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.saveHttp()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      back () {
        this.$router.push('/enterpriseAccount')
      },
      saveHttp () {
        let _this = this
        let enterpriseName = this.ruleForm2.enterpriseName === this.ruleForm2.oldEnterpriseName ?
          this.ruleForm2.oldEnCode : this.ruleForm2.enterpriseName

        this.$http.get('/backuser/save', {
          params: {
            userName: this.ruleForm2.userName,
            account: this.ruleForm2.account,
            password: this.ruleForm2.password,
            type: this.ruleForm2.enterpriseType,
            uid: this.uid === 0 ? '' : this.uid,
            enCode: enterpriseName
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '保存成功',
                type: 'success'
              })
              _this.$router.push('/enterpriseAccount')
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      resetPass () {
        let _this = this
        this.$http.get('/backuser/resetPassword', {
          params: {
            uid: this.uid
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.resetHint = '密码已重置为123456'
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true
          setTimeout(() => {
            this.loading = false
            this.optionsName = []
            this.getEnterpriseList(query)
          }, 200);
        } else {
          this.optionsName = []
        }
      },
      getEnterpriseList(query) {
        let _this = this
        this.$http.get('/enterprise/query', {
          params: {
            enName: query,
            type: this.ruleForm2.enterpriseType
          }
        })
          .then(function (res) {
            if (_this.ruleForm2.enterpriseType === '2') {
              let property = res.resultData.property
              property.map((v, i) => {
                _this.optionsName.push({label: v.propertyName, value: v.enCode})
              })
            } else {
              let enterpriseList = res.resultData.enterprise
              enterpriseList.map((v, i) => {
                _this.optionsName.push({label: v.enName, value: v.enCode})
              })
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style>
    .addEnterpriseAccount ul {

    }
    .addEnterpriseAccount >div>.el-checkbox {
        margin-bottom: 10px;
    }
    .addEnterpriseAccount ul li {
        margin: 10px;
        line-height: 32px;
        clear: both;
    }
    .addEnterpriseAccount ul li > i {
        font-style: normal;
        color: red;
    }
    .addEnterpriseAccount ul li > span {
        float: left;
        width: 90px;
        text-align: right;
    }
    .addEnterpriseAccount ul li > span::before {
        content: '*';
        color: red;
    }
    .addEnterpriseAccount ul li > div {
        width: 200px;
    }
    .addEnterpriseAccount .operationBtn {
        text-align: center;
        margin-top: 60px;
    }
    .addEnterpriseAccount .operationBtn button {
        margin-right: 20px;
        padding: 10px 40px;
    }
    .addEnterpriseAccount .el-checkbox {
        margin-right: 30px;
    }
    .addEnterpriseAccount .el-checkbox+.el-checkbox {
        margin-left: 0;
    }
    .addEnterpriseAccount .el-input {
        width: 250px;
    }
</style>



