<template>
  <add-activity v-show="isShow" :id="this.id" type="edit" ref="activity">

  </add-activity>
</template>

<script>

  import AddActivity from './addActivity.vue';

  import _ from 'lodash';

  export default{

    components:{
      AddActivity
    },

    created(){
      const id = this.id = _.get(this,'$route.params.id');
      if(id){
         this.isShow = true;
      }
    },

    mounted(){
      this.$http.get("/activity/get",{
          params:{ id:this.id }
      }).then((res)=>{

        const data = _.get(res,"resultData.activity",{});

        this.$refs.activity.update(data);

      })
    },

    data(){
      return {
          isShow:false,
          data:{}
      }
    }
  }

</script>

<style>
</style>



