import Vue from 'vue'
import ElementUI from 'element-ui'

import 'element-ui/lib/theme-chalk/index.css'
import '../static/global.css'

import axios from './utils/http'

let TYPE = 'advertiser';  //生产模式切换模式->> property || advertiser || superAdministrator

let router,App;

//生产模式打包
if(process.env.NODE_ENV === "production") {
  switch (process.env.BUILD_TYPE) {
    case "wy" : TYPE = "property"; break;
    case "gg" : TYPE = "advertiser" ; break;
    default : TYPE = "superAdministrator"
  }
}

router = require(`./app/${TYPE}/router`).default;

App = require( `./app/${TYPE}/App.vue`).default;

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.prototype.$http = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



