<template>
  <div id="addDistrict" class="addDistrict">
    <h3 v-if="districtId === '0'">新增小区</h3>
    <h3 v-else>编辑小区</h3>
    <el-form
      :model="addDistrict"
      status-icon
      :rules="rules2"
      ref="addDistrict"
      label-width="100px"
      class="demo-ruleForm"
      label-position="center"
      size="small"
    >
      <el-form-item label="小区名称" prop="name">
        <el-input v-model="addDistrict.name"></el-input>
      </el-form-item>
      <el-form-item label="小区地址" prop="linkAge">
        <el-cascader
          :options="addDistrict.options"
          v-model="addDistrict.linkAge"
          @change="handleChange"
        ></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="addDistrict.address" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="小区详情" prop="detail">
        <el-input type="textarea" style="width: 60%" :rows="2" v-model="addDistrict.detail"></el-input>
      </el-form-item>
      <el-form-item label="小区规模" prop="scale">
        <el-input type="textarea" style="width: 60%" :rows="2" v-model="addDistrict.scale" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="广告位数量" prop="num">
        <el-input v-model="addDistrict.num" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="广告负责人" prop="person">
        <el-input v-model="addDistrict.person"></el-input>
      </el-form-item>
      <el-form-item label="负责人手机" prop="phone">
        <el-input v-model="addDistrict.phone" auto-complete="off"></el-input>
      </el-form-item>
      <br>
      <el-form-item>
        <el-button class="submitBtn" type="primary" @click="submitForm('addDistrict')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import Pca from '../../../../utils/pca.json'

  export default {
    name: 'addDistrict',
    data () {
      var validateName = (rule, value, callback) => {
        if (!(value.toString().trim())) {
          return callback(new Error('请输入小区名称'))
        } else if (value.length > 30) {
          return callback(new Error('最多输入30字符'))
        } else {
          callback()
        }
      }
      var validateLinkAge = (rule, value, callback) => {
        if (!value.length) {
          callback(new Error('请输入省市区'))
        } else {
          callback()
        }
      }
      var validateAddress = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入正确小区地址'))
        } else if (value.length > 50) {
          callback(new Error('最多输入50字符'))
        } else {
          callback()
        }
      }
      var validateNum = (rule, value, callback) => {
        if (value && !(/^[0-9]{0,9}$/.test(value))) {
          callback(new Error('广告位数量必须为0-9位正整数'))
        } else {
          callback()
        }
      }
      var validatePerson = (rule, value, callback) => {
        if (value && value.length > 10) {
          callback(new Error('最多输入10字符'))
        } else {
          callback()
        }
      }
      var validatePhone = (rule, value, callback) => {
        if (value && !(/^1[34578]\d{9}$/.test(value))) {
          callback(new Error('请输入正确负责人手机号'))
        } else {
          callback()
        }
      }

      return {
        options: [],
        selectedOptions3: ['zujian', 'data', 'tag'],
        districtId: this.$route.query.districtId || '0',
        addDistrict: {
          name: '',
          address: '',
          linkAge: [],
          options: [],
          detail: '',
          scale: '',
          num: '',
          person: '',
          phone: ''
        },
        rules2: {
          name: [
            { required: true, validator: validateName, trigger: 'change' }
          ],
          linkAge: [
            { required: true, validator: validateLinkAge, trigger: 'change' }
          ],
          address: [
            { required: true, validator: validateAddress, trigger: 'change' }
          ],
          detail: [
            {message: '请输入正确小区详情', trigger: 'blur'},
            {min: 0, max: 100, message: '请输入1-100字', trigger: 'change'}
          ],
          scale: [
            {message: '请输入正确小区规模', trigger: 'blur'},
            {min: 0, max: 100, message: '请输入1-100字', trigger: 'change'}
          ],
          num: [
            { validator: validateNum, trigger: 'change' }
          ],
          person: [
            { validator: validatePerson, trigger: 'change' }
          ],
          phone: [
            { validator: validatePhone, trigger: 'change' }
          ]
        }
      }
    },
    components: {
      Pca
    },
    mounted () {
      if (this.districtId != 0) { this.get() }
      Pca && Pca.map((v, i) => {
        let json = {
          value: v.name,
          label: v.name,
          children: []
        }
        v.childs.map((c, y) => {
          json.children.push({value: c.name, label: c.name, children: []})
          c.childs.map((a, r) => {
            json.children[y].children.push({value: a.name, label: a.name})
          })
        })
        this.addDistrict.options[i] = json
      })
    },
    methods: {
      get () {
        let _this = this
        this.$http('/community/get', {
          params: {
            cid: this.districtId
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              const community = res.resultData.community
              _this.addDistrict.name = community.cName
              _this.addDistrict.address = community.address
              _this.addDistrict.linkAge = [community.province, community.city, community.district]
              _this.addDistrict.detail = community.cDesc
              _this.addDistrict.scale = community.cScale
              _this.addDistrict.num = community.posterNumber && Number(community.posterNumber)
              _this.addDistrict.person = community.principal
              _this.addDistrict.phone = community.principalPhone
              return _this
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.saveDistrict()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      saveDistrict () {
        let _this = this
        this.$http.post('/community/save', {
          cName: this.addDistrict.name,
          province: this.addDistrict.linkAge[0],
          city: this.addDistrict.linkAge[1],
          district: this.addDistrict.linkAge[2],
          address: this.addDistrict.address,
          cDesc: this.addDistrict.detail,
          cScale: this.addDistrict.scale,
          posterNumber: this.addDistrict.num,
          principal: this.addDistrict.person,
          principalPhone: this.addDistrict.phone,
          cid: this.districtId == '0' ? '' : this.districtId
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              _this.$message({
                message: '保存成功',
                type: 'success'
              })
              _this.$router.push('/districtManagement')
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      handleChange (value) {
        this.addDistrict.linkAge = value
        return this.addDistrict.linkAge
      }
    }

  }
</script>

<style scoped>
  .addDistrict > h3 {
    line-height: 20px;
    border-bottom: 1px solid #ededed;
    padding-bottom: 10px;
    position: relative;
  }
  .addDistrict > h3::after {
    content: '';
    width: 80px;
    height:2px;
    display: block;
    position: absolute;
    bottom: 0px;
    background: #3a8ee6;
  }
  .addDistrict form .el-form-item__content {
    /*width: 260px;*/
  }
  .addDistrict .submitBtn {
    width: 100px;
    height: 40px;
    float: none;
  }
  .addDistrict .el-input {
    width: 250px;
  }
</style>



