<template>
  <div id="sideBar">

    <el-row class="tac1">
      <el-col :span="24">
        <el-menu
          key="1"
          v-bind:default-active="currentKey"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="changeMenu"
          background-color="#324158"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
          unique-opened
          v-for="(m, i) in menuList"
        >
          <el-submenu key="m.key" v-bind:index="m.path" v-if="m.level == 2 && pids.indexOf(m.key.toString()) !== -1">
            <template slot="title">
              <b v-bind:class="m.active" v-if="currentKey === m.path"></b>
              <b v-bind:class="m.class" v-else></b>
              <span slot="title">{{ m.name }}</span>
            </template>
            <el-menu-item key="c.key" v-bind:index="c.path" v-for="c in m.children">{{ c.name }}</el-menu-item>
          </el-submenu>

          <el-menu-item key="m.key" v-bind:index="m.path" v-else-if="m.level == 1 && pids.indexOf(m.key.toString()) !== -1">
            <b v-bind:class="m.active" v-if="currentKey === m.path"></b>
            <b v-bind:class="m.class" v-else></b>
            <span slot="title">{{ m.name }}</span>
          </el-menu-item>
        </el-menu>
      </el-col>
    </el-row>

  </div>
</template>

<script>
  export default {
    name: 'sideBar',
    data () {
      return {
        currentKey: '',
        pids: JSON.parse(sessionStorage.getItem('userInfo')).pids.split(','),
        menuList: [
          {
            key: 1,
            level: 1,
            path: '/districtManagement',
            name: '小区管理',
            class: 'icon1',
            active: 'icon1_active'
          },
          {
            key: 10,
            level: 1,
            path: '/propertyManagement',
            name: '物业管理',
            class: 'icon2',
            active: 'icon2_active'
          },
          {
            key: 2,
            level: 1,
            path: '/advertiserManagement',
            name: '广告主管理',
            class: 'icon3',
            active: 'icon3_active'
          },
          {
            key: 3,
            level: 1,
            path: '/made_actively',
            name: '发布活动',
            class: 'icon4',
            active: 'icon4_active'
          },
          {
            key: 5,
            level: 1,
            path: '/rechargeRecord',
            name: '充值记录',
            class: 'icon5',
            active: 'icon5_active'
          },
          {
            key: 11,
            level: 1,
            path: '/callSetting',
            name: '话费设置',
            class: 'icon11',
            active: 'icon11_active'
          },
          {
            key: 12,
            level: 1,
            path: '/trafficSetting',
            name: '流量设置',
            class: 'icon12',
            active: 'icon12_active'
          },
          {
            key: 7,
            level: 2,
            path: '/financialManagement',
            name: '财务管理',
            class: 'icon6',
            active: 'icon6_active',
            children: [
              {
                key: '5-1',
                path: '/propertyPresentAudit',
                name: '物业提现审核',
              },{
                key: '5-2',
                path: '/userPresentAudit',
                name: '用户提现审核',
              },{
                key: '5-3',
                path: '/platformPumping',
                name: '平台抽佣',
              }
            ]
          },
          {
            key: 4,
            level: 1,
            path: '/userManagement',
            name: '用户管理',
            class: 'icon7',
            active: 'icon7_active'
          },
          {
            key: 8,
            level: 1,
            path: '/enterpriseAccount',
            name: '企业账号',
            class: 'icon8',
            active: 'icon8_active'
          },
          {
            key: 6,
            level: 1,
            path: '/opinionFeedback',
            name: '意见反馈',
            class: 'icon9',
            active: 'icon9_active'
          },
          {
            key: 9,
            level: 1,
            path: '/permissionManagement',
            name: '权限管理',
            class: 'icon10',
            active: 'icon10_active'
          }
        ],
        relationship: {
          '/addDistrict': 'districtManagement',
          '/addAdvertiser': 'advertiserManagement',
          '/made_actively_add': 'made_actively',
          '/addEnterpriseAccount': 'enterpriseAccount',
          '/addpersonnel': 'permissionManagement'
        }
      }
    },
    created: function () {
      let url = this.$route.path.split('/')[1] === '' ? 'districtManagement' : this.$route.path.split('/')[1]
      this.currentKey = this.relationship[url] ? this.relationship[url] : url
    },
    methods: {
      handleOpen (key, keyPath) {
        console.warn(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      },
      changeMenu (indexPath) {
        this.currentKey = this.relationship[indexPath] ? this.relationship[indexPath] : indexPath
        return this.currentKey
      }
    }
  }
</script>

<style>
  .tac1 div > ul > li b {
    width: 24px;
    height: 24px;
    display: inline-block;
  }
  .icon1 {
    background: url('../assets/icon.png') 0 0 no-repeat;
  }
  .icon2 {
    background: url('../assets/icon.png') -24px 0 no-repeat;
  }
  .icon3 {
    background: url('../assets/icon.png') -48px 0 no-repeat;
  }
  .icon4 {
    background: url('../assets/icon.png') -72px 0 no-repeat;
  }
  .icon5 {
    background: url('../assets/icon.png') -97px 0 no-repeat;
  }
  .icon6 {
    background: url('../assets/icon.png') -121px 0 no-repeat;
  }
  .icon7 {
    background: url('../assets/icon.png') -146px 0 no-repeat;
  }
  .icon8 {
    background: url('../assets/icon.png') -216px 0 no-repeat;
  }
  .icon9 {
    background: url('../assets/icon.png') -24px 0 no-repeat;
  }
  .icon10 {
    background: url('../assets/icon.png') -192px 0 no-repeat;
  }
  .icon11 {
    background: url('../assets/icon.png') -240px 0 no-repeat;
  }
  .icon12 {
    background: url('../assets/icon.png') -265px 0 no-repeat;
  }
  .icon1_active {
    background: url('../assets/icon.png') 0 -24px no-repeat;
  }
  .icon2_active {
    background: url('../assets/icon.png') -24px -24px no-repeat;
  }
  .icon3_active {
    background: url('../assets/icon.png') -48px -24px no-repeat;
  }
  .icon4_active {
    background: url('../assets/icon.png') -72px -24px no-repeat;
  }
  .icon5_active {
    background: url('../assets/icon.png') -97px -24px no-repeat;
  }
  .icon6_active {
    background: url('../assets/icon.png') -121px -24px no-repeat;
  }
  .icon7_active {
    background: url('../assets/icon.png') -146px -24px no-repeat;
  }
  .icon8_active {
    background: url('../assets/icon.png') -216px -24px no-repeat;
  }
  .icon9_active {
    background: url('../assets/icon.png') -24px -24px no-repeat;
  }
  .icon10_active {
     background: url('../assets/icon.png') -192px -24px no-repeat;
   }
  .icon11_active {
    background: url('../assets/icon.png') -240px -24px no-repeat;
  }
  .icon12_active {
    background: url('../assets/icon.png') -265px -24px no-repeat;
  }
</style>



