<template>
  <div id="propertyPresentAudit">
    <div class="searchModule">
      <span>物业</span>
      <el-input size="small" v-model="propertyName" placeholder="请输入内容"></el-input>
      <span>&nbsp;&nbsp;&nbsp;&nbsp;状态</span>
      <el-select v-model="status" size="small" placeholder="全部">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button size="small" type="primary" @click="search">搜索</el-button>
    </div>

    <el-table
      :data="tableData"
      border
      header-cell-class-name="advertisement_table_header"
      cell-class-name="advertisement_table_cell"
      style="width: 100%">
      <el-table-column
        v-for="(item,key) in columns"
        :key="key"
        :prop="item.prop"
        :label="item.label">
      </el-table-column>
      <el-table-column
        key="9"
        label="操作"
        width="80px">
        <template slot-scope="scope">
          <a  v-if="scope.row.serialNumber == ''" @click="operation(scope.$index, scope.row)">处理</a>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination" v-if="tableData.length">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="pageNum"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="40%"
      @close="resetForm('ruleForm2')"
    >

      <el-form
        :model="row"
        status-icon
        :rules="rules2"
        ref="ruleForm2"
        label-width="130px"
        class="demo-ruleForm propertyForm"
        label-position="center"
        size="small"
      >
        <el-form-item label="物业：">
          <span>{{ row.propertyName }}</span>
        </el-form-item>
        <el-form-item label="开户银行：">
          <span>{{ row.bank }}</span>
        </el-form-item>
        <el-form-item label="银行卡号：">
          <span>{{ row.bankAccount }}</span>
        </el-form-item>
        <el-form-item label="户名：">
          <span>{{ row.accountUser }}</span>
        </el-form-item>
        <el-form-item label="提现金额：">
          <span>{{ row.withdrawMoney }}</span>
        </el-form-item>
        <el-form-item label="交易流水号：" prop="serialNumber">
          <el-input v-model="row.serialNumber" style="width: 300px" size="small" placeholder="请填写银行打款的流水号">
          </el-input>
        </el-form-item>

        <div>
          <el-form-item>
            <el-button style="marginLeft: 50px" @click="resetForm('ruleForm2')">取消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: 'propertyPresentAudit',
    data () {
      var validateSerialNumber = (rule, value, callback) => {
        if (!value || value.trim() == '' || value.length > 50) {
          return callback(new Error('请输入正确交易流水号'))
        } else {
          callback()
        }
      }
      return {
        propertyName: '',
        pageNum: 1,
        tableData: [],
        total: 0,
        lastPropertyName: '',
        lastStatus: '',
        dialogVisible: false,
        columns: [
          {prop: 'serialNumber', label: '流水号'},
          {prop: 'propertyName', label: '物业'},
          {prop: 'withdrawMoney', label: '提现金额（元）'},
          {prop: 'bank', label: '开户银行'},
          {prop: 'bankAccount', label: '银行卡号'},
          {prop: 'accountUser', label: '户名'},
          {prop: 'status', label: '状态'},
          {prop: 'uName', label: '操作人'}
        ],
        options: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '待处理'
        }, {
          value: '1',
          label: '处理完成'
        }],
        status: '',
        row: {
          serialNumber: ''
      },
        rules2: {
          serialNumber: [
            { required: true, validator: validateSerialNumber, trigger: 'change' }
          ]
        },
        operationIndex: ''
      }
    },
    mounted () {
      this.getList()
    },
    methods: {
      search() {
        this.pageNum = 1
        this.lastPropertyName = this.propertyName
        this.lastStatus = this.status
        this.getList()
      },
      getList () {
        let _this = this
        this.$http.get('/finance/withdraw/property/list', {
          params: {
            pageNum: this.pageNum,
            pageSize: 10,
            propertyName: this.propertyName,
            status: this.status
          }
        })
          .then(function (res) {
            if (res.errorCode === '0') {
              let propertyWithdraw= res.resultData.propertyWithdraw;
              propertyWithdraw.map((v,i)=>{
                return v.status == 0 ? v.status = '待处理' : v.status = '处理完成'
              })
              _this.tableData = propertyWithdraw
              _this.total = res.resultData.total
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      operation (index, row) {
        this.dialogVisible = true
        this.operationIndex = index
        for (var key in row) {
          this.row[key] = row[key]
        }
      },
      submitForm (formName) {
        let _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.confirmFormUpdate()
          } else {
            return false
          }
        })
      },
      confirmFormUpdate() {
        let _this = this
        this.$http.post('/finance/withdraw/property/handle', {
          pwId: this.row.pwId,
          serialNumber: this.row.serialNumber.trim()
        })
          .then(function(res) {
            if (res.errorCode === '0') {
              if (_this.operationIndex == (_this.total - 1)%10) {
                _this.pageNum = Math.ceil((_this.total - 1)/10)
              }
              _this.resetForm('ruleForm2')
              _this.getList()
            }
          })
      },
      resetForm(formName) {
        this.row = {serialNumber: ''}
        this.$refs[formName].resetFields();
        this.dialogVisible = false
      },
      handleCurrentChange (val) {
        this.pageNum = val
        this.propertyName = this.lastPropertyName
        this.status = this.lastStatus
        this.getList()
      }
    }
  }
</script>

<style>

</style>



